<template>
<div class="tool-box" ref="main" :class="{overlay:show_overlay||show_amix}" 
    @touchstart="currentStart" 
    @touchmove="currentMove" 
    @touchend="changed()">

    <div class="main-line going" ref="mainLine" 
        :style="mainLineStyle">

        <div v-if="!postermode" @click="$emit('on_poster_click')" class="video-post-btn">
            <span v-if="!poster_url" class="material-icons">insert_photo</span>
            <img v-else :src="poster_url" @error="posterLoadError" />
            <span class="text"></span>
        </div>
        <div class="main-timeline" style="">
            <template v-for="(i) in timelines">
                <!-- ,  -->
                <TimelineItem v-if="i.ending" :key="i.id" 
                    @change="timelineChange(i.id,$event)"
                    @active="timelineActive(i.id,$event)"
                    @click.native="endingClick"
                    :pps="pps"
                    :dragging="true"
                    :active="false"
                    :focus="postermode?false:i.focus"
                    :start_sec="i.start_sec"
                    :end_sec="i.end_sec"
                    :total_sec="i.total_sec" 
                    :offset_sec="i.offset_sec"
                    :left_drag_offset="i.left_drag_offset"
                    :min_sec="video_min_sec" >
                    <div class="video-ending">
                        <span></span>
                    </div>
                </TimelineItem>
                <!--  -->
                <TimelineItem v-else :key="i.id" 
                    @change="timelineChange(i.id,$event)"
                    @active="timelineActive(i.id,$event)"
                    :pps="pps" 
                    :dragging="dragging"
                    :focus="postermode?false:i.focus"
                    :active="postermode?false:i.active"
                    :start_sec="i.start_sec"
                    :end_sec="i.end_sec"
                    :total_sec="i.total_sec" 
                    :offset_sec="i.offset_sec"
                    :left_drag_offset="i.left_drag_offset"
                    :min_sec="video_min_sec" >
                    <TimelineItemThumb :time_sec="time_sec - i.offset_sec" :pps="pps" :timeline="i" />
                </TimelineItem>
            </template>
        </div>

        <div v-if="!postermode && !hasEnding" @click="endingClick" class="video-ending-btn">
            <span>+</span>
        </div>

        <div v-if="!postermode && show_overlay" class="sub-timeline" style="margin-top:35px;">
            <template v-for="(i) in overlay">
                <TimelineOverlayItem v-if="i.type & overlay_type" :key="i.id" 
                    @change="overlayChange(i.id,$event)"
                    @active="overlayActive(i.id,$event)"
                    :id="i.id"
                    :type="i.type"
                    :active="i.active"
                    :pps="pps" 
                    :end_sec="i.end_sec"
                    :offset_sec="i.offset_sec" >
                </TimelineOverlayItem>
            </template>
        </div>
        <div v-if="!postermode && show_amix" class="sub-timeline" style="margin-top:35px;">
            <TimelineOverlayItem v-for="(i) in amix" :key="i.id" 
                @change="amixChange(i.id,$event)"
                @active="amixActive(i.id,$event)"
                :id="i.id"
                :is_amix="true"
                :active="i.active"
                :pps="pps" 
                :end_sec="i.end_sec"
                :offset_sec="i.offset_sec" >
            </TimelineOverlayItem>
        </div>

    </div>

    <div class="time-tick" ref="current">
        <div class="current-time" :class="{dragging:dragging}">
            <!-- <mu-icon v-if="modal.playing" type="play_arrow" />
            <mu-icon v-else type="pause" /> -->
            {{currentTimeStr}}
        </div>
    </div>
    <div class="total-time-tick">
        {{totalTimeStr}}
    </div>
</div>
</template>

<style lang="less" scoped>
.tool-box {
    bottom: 0;
    position: relative;
    display: flex;
    width: 100%;
    height: 80px;
}
.tool-box.overlay{
    height:130px;
}

.main-line.going {
    transition:transform 0s linear
}
.main-timeline {
    //wkwebview  post_btn  maintimeline 
    margin-top:0.1px;
}
.main-line {
    width: 100%;
    height: 34px;
    position: absolute;
    top: 50%;
    left:50%;
    margin-top: -17px;
    background: transparent;
    will-change: transform;

    .video-post-btn {
        position: absolute;
        top: 0;
        left: -100px;
        background: gray;
        border-radius: 5px;
        height: 34px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        padding: 5px;
        color: white;
        font-size: 12px;

        img {
            display: block;
            width: 24px;
            height: 24px;
            position: relative;
            border-radius: 5px;
            object-fit: cover;
        }
        .material-icons {
            font-size: 32px;
        }
        .text {
            display: block;
            position: relative;
            margin-left: 5px
        }
    }

    .main-timeline {
        z-index: 1;
    }

    .video-ending-btn {
        position: absolute;
        right: -121px;
        background: gray;
        border-radius: 5px;
        height: 34px;
        padding:5px 0;
        width: 7em;
        color: white;
        font-size: 12px;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .video-ending {
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        font-size: smaller;
    }

}

.time-tick {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 45px;
    width: 2px;
    background-color: white;
    margin-left: -1px;
    margin-top: -25px;
    border-radius: 10px;
    padding-top:5px;
    box-sizing: content-box;
    display: flex;
    justify-content: center;
}

.tick-mask {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

.current-time.dragging {
    font-size:12px;
    opacity: 1;
}
.current-time {
    position: absolute;
    background: white;
    color: black;
    border-radius: 2px;
    padding: 0 3px;
    font-size: 8px;
    top: -15px;
    opacity: 0.5;
    display: flex;
    align-items: center;
    white-space: nowrap;
    transition: font-size 0.1s
}

.total-time-tick {
    position: absolute;
    right: 0%;
    top: 0%;
    height: 45px;
    color:white;
    font-size:10px;
    padding:0 5px;    
    top: 50%;
    margin-top: -40px;
}
</style>

<script>
/***
 * position, timelines
 * 
 * position, timelines
 * 
 * position, timelines
 * 
 * timeChange, timelinesChange
 */
import TimelineItem from "@/components/TimelineItem";
import TimelineOverlayItem from "@/components/TimelineOverlayItem";
import TimelineItemThumb from "@/components/TimelineItemThumb";
import ClipModel from "@/model/ClipModel";
import Editlist from "@/model/Editlist";
import Const from "@/const"

let Helper = {
    toTimeStr(sec=0) {
        let min = sec / 60;
        let hour = min / 60;
        let millisec = Math.floor((sec % 1) * 10);
        return (
            Math.floor(hour) +
            ":" +
            Math.floor(min) +
            ":" +
            Math.floor(sec % 60) +
            "." +
            millisec
        );
    }
}
export default {
    components:{
        TimelineItem,
        TimelineOverlayItem,
        TimelineItemThumb
    },
    props: ["time_sec_in", "timelines_in", "playing", "show_overlay", "show_amix", "postermode", "poster", "overlay_type"],
    data() {
        return {
            Const,
            pps:screen.width / 10,
            timelines:this.timelines_in,
            overlay_in:Editlist.editlist.overlay,
            mosic_in:Editlist.editlist.mosic,
            amix:Editlist.editlist.amix,
            video_min_sec: Editlist.editlist.video_min_sec,

            time_sec: Editlist.editlist.time_sec,
            mainLineStyle:{
                width:0,
                transform:"",
                "transition-duration":"0s"
            },
            poster_url:"",
            dragging:false,
            timelineChanging:false,
            timer: null
        };
    },
    watch: {
        overlay(){
            window.console.log("over watch");
        },
        async playing(v){
            if(v){
                // s
                let UpdateRate = 0.5;

                this.mainLineStyle["transition-duration"] = UpdateRate+"s";

                        window.console.log(`begin playing`)
                // let fun = async ()=>{
                //     if(this._moveSpeed)return; //not dragging

                //     try{
                //         let cur = await new Promise(
                //             UGCApp.ffplay.position) ;

                //         window.console.log("position:"+cur);

                //         if(isNaN(cur)) {
                //             // NaN
                //             //  NaN
                //             return;
                //         }

                //         if(cur < 0 && cur > -2) cur = 0;
                //         if(cur > this.total_sec) cur = this.total_sec;
                //         if(cur < this.time_sec) cur = this.time_sec;
                        
                //         //css
                //         // let style = window.getComputedStyle(this.$refs.mainLine);
                //         // let x = Number.parseFloat(style.transform.split(/[(,)]/)[5]);
                //         // if(x < -this.total_sec * this.pps)
                //         //     x = -this.total_sec * this.pps;
                //         // this.mainLineStyle.transform = "translateX("+(x)+"px)";
                        
                //         // this.mainLineStyle.transform = "translateX(-"+(
                //         //     Math.min(this.total_sec, cur + UpdateRate) * this.pps
                //         // )+"px)";
                //         this.mainLineStyle.transform = "translateX(-"+(
                //             Math.min(this.total_sec, cur) * this.pps
                //         )+"px)";

                //         this.time_sec = cur;
                //         Editlist.editlist.time_sec = cur;

                //     }catch(e){
                //         window.console.log(e);
                //     }
                // }

                let fun = async ()=>{
                    if(this._moveSpeed)return; //not dragging
                    try{
                        let cur = await new Promise(
                            UGCApp.ffplay.position) ;

                        window.console.log(`position:${cur} total:${this.total_sec}`)

                        // if(cur < this.time_sec ||
                        //     cur > this.time_sec+UpdateRate)
                        //     cur = this.time_sec+UpdateRate;
                        if(!(cur >= 0))return;

                        if(cur > this.total_sec) cur = this.total_sec;

                        // let nxtCur = cur + UpdateRate;

                        //css
                        let style = window.getComputedStyle(this.$refs.mainLine);
                        let x = Number.parseFloat(style.transform.split(/[(,)]/)[5]);

                        let curPos = cur;
                        let timeRemain = this.total_sec - curPos;
                        let timeRemain2 = ((this.total_sec) * this.pps + x)/this.pps;

                        this.mainLineStyle["transition-duration"] = Math.max(0,timeRemain)+"s";

                        this.mainLineStyle.transform = `translateX(${x}px)`;
                        this.mainLineStyle.transform = "translateX(-"+(
                            (this.total_sec+Math.random()/1000) * this.pps
                        )+"px)";

                        window.console.log(`left:${(this.total_sec) * this.pps + x} remain:${timeRemain}/${timeRemain2} speed:${((this.total_sec) * this.pps + x) / timeRemain}`)

                        // if(nxtCur >= this.total_sec - 5){
                        //     nxtCur = this.total_sec;
                        //     this.mainLineStyle["transition-duration"] = (x/this.pps+this.total_sec)+"s";
                        //     window.console.log(`going:${nxtCur}/${this.total_sec} speed:${cur-this.total_sec}`)
                        // }
                        // else
                        //     window.console.log(`going:${nxtCur}/${this.total_sec} speed:${UpdateRate}`)
                        // this.mainLineStyle.transform = "translateX("+(x)+"px)";
                        
                        // this.mainLineStyle.transform = "translateX(-"+(
                        //     (nxtCur) * this.pps
                        // )+"px)";

                        window.console.log(`move: ${this.mainLineStyle.transform}`)

                        this.time_sec = cur;
                        if(!this.playing){
                            clearInterval(this._goingTmr);
                            //
                            window.console.log(`stop ${this.mainLineStyle.transform}`)
                            let style = window.getComputedStyle(this.$refs.mainLine);
                            let x = Number.parseFloat(style.transform.split(/[(,)]/)[5]);
                            this.mainLineStyle["transition-duration"] = "0s";
                            this.mainLineStyle.transform = "translateX("+(x)+"px)";
                        }

                    }catch(e){
                        window.console.log(e);
                    }
                }

                fun();
                
                // this._goingTmr2 = setInterval(async ()=>{
                //     let cur = await new Promise(
                //         UGCApp.ffplay.position) ;
                //     this.time_sec = cur;
                // }, 0.5*1000);
                // this._goingTmr = setInterval(fun, 20);//UpdateRate*1000);
                this._goingTmr = setInterval(fun, UpdateRate*1000);

            }
            else{

                {
                    clearInterval(this._goingTmr);

                    //
                    window.console.log(`stop ${this.mainLineStyle.transform}`)
                    let style = window.getComputedStyle(this.$refs.mainLine);
                    let x = Number.parseFloat(style.transform.split(/[(,)]/)[5]);
                    this.mainLineStyle["transition-duration"] = "0s";
                    this.mainLineStyle.transform = "translateX("+(x)+"px)";
                }

                //
                try{
                    let cur = await new Promise(
                        UGCApp.ffplay.position);
                    if((cur >= 0)) {
                        this.time_sec = cur;
                        Editlist.editlist.time_sec = cur;
                    }
                    // this.update(true);
                }catch(e){
                    window.console.log(e);
                }

                //
                if(Math.abs(this.time_sec - this.total_sec) < 1){

                    this.mainLineStyle["transition-duration"] = "0.1s";

                    this.mainLineStyle.transform = "translateX(-"+(
                        (this.time_sec+Math.random()/1000) * this.pps
                    )+"px)";
                    setTimeout(()=>{
                        this.mainLineStyle["transition-duration"] = "0s";
                    },150);
                }
                else{ // lag
                    // this.mainLineStyle["transition-duration"] = "0s";
                    // this.mainLineStyle.transform = "translateX("+(x)+"px)";
                }

                
                // clearInterval(this._goingTmr2);
            }
        },
        time_sec_in(v){
            this.time_sec = v;
            this.update(true);
        },
        time_sec(v) {
            this.update();
            
            Editlist.editlist.time_sec = v;
            this.refreshUI();
        },
        timelines_in(v){

            let srcIDs = this.timelines.map(o=>o.id).join(",");
            let inIDs = v.map(o=>o.id).join(",");
            if(srcIDs != inIDs)
                this.timelines = v;
                
            if(this.timelines.length != v.length)
                this.timelines = v;
            
            this.update(true);
            
        },
        async poster(url){
            // window.console.log("poster url:"+url);
            this.poster_url = await ClipModel.Util.getCdvUrl(url)+"?"+Math.random();
        }
    },
    computed: {
        overlay() {
            return [...this.overlay_in, ...this.mosic_in];
        },
        total_sec(){
            if(!this.timelines.length)return 0;
            if(this.timelines.length == 1)
                return this.timelines[0].end_sec - this.timelines[0].start_sec;
                
            return this.timelines.reduce((pre,nxt,idx)=>{
                if(idx == 1)
                    pre = pre.end_sec - pre.start_sec;
                
                nxt.offset_sec = pre;

                return pre + nxt.end_sec - nxt.start_sec;
            });
        },
        currentTimeStr() {
            // console.log("update time:"+this.modal.current)
            return Helper.toTimeStr(
                this.time_sec
            ).replace("0:0","0");
        },
        totalTimeStr() {
            return Helper.toTimeStr(
                this.total_sec
            ).split(".")[0].replace("0:0","0");
        },
        hasEnding() {
            return this.timelines.find(e => e.ending);
        }
    },
    methods: {
        async updateFrame(){
            // let SynSpan = 1; //
            // if(this.playing && !this._moveSpeed){
            //     let cur = await new Promise(
            //         UGCApp.ffplay.position);
            //     this.time_sec = cur;
            // }

            // requestAnimationFrame(this.updateFrame);
        },
        seek(sec){
            if(this.dragging)return;

            this.time_sec = sec;
            this.update(true);
        },
        // idx 
        time_sec_idx(idx){
            let found = this.timelines.filter((o,i) => {
                return i == idx && o.offset_sec <= this.timeSec 
                    && o.offset_sec + (o.end_sec - o.start_sec) > this.timeSec;
            })
            if(found.length)
                return this.time_sec - found[0].offset_sec;
            else return -1;
        },
        update(computeTransform){
            if(this.timelineChanging) {
                return;
            }

            //
            this.mainLineStyle.width = this.timelines.reduce((pre,nxt)=>{
                return pre + nxt.end_sec - nxt.start_sec
            }, 0) * this.pps + "px";

            //
            if(this._moveSpeed || computeTransform) {
                this.mainLineStyle.transform = "translateX(-"+(
                    this.time_sec * this.pps
                )+"px)";
            }

            //
            this.timelines.reduce((pre,nxt)=>{
                nxt.offset_sec = pre;
                return pre + nxt.end_sec - nxt.start_sec;
            }, 0);

        },
        overlayActive(id, active){
            if(active){
                this.overlay.forEach((v)=>{
                    v.active = v.id == id;
                })
                this.$emit("overlayActive", this.overlay.filter(o=>o.id==id)[0]);
            }
            else {
                this.overlay.filter(o=>o.id==id)[0].active = false;
                this.$emit("overlayActive");
            }
        },
        overlayChange(id, event){
            let found = this.overlay.filter( o=>o.id==id )[0];
            found.offset_sec = event.offset_sec;
            found.end_sec = event.end_sec;
                
            if(event.overallDragging) {
                this.time_sec = found.offset_sec;
                this.changed({
                    time_sec: this.time_sec
                });
                this.update(true);
            }
            
            // window.console.log("overlayCHange:"+event.end)
            if(event.end)
                this.$emit("overlayChange", this.overlay.filter(o=>o.id==id)[0]);
        },
        amixChange(id, event){
            let found = this.amix.filter( o=>o.id==id )[0];
            found.offset_sec = event.offset_sec;
            found.end_sec = event.end_sec;
            if(event.end)
                this.$emit("amixChange", this.amix.filter(o=>o.id==id)[0]);

        },
        amixActive(id, active){
            if(active){
                this.amix.forEach((v)=>{
                    v.active = v.id == id;
                })
                this.$emit("amixActive", this.amix.filter(o=>o.id==id)[0]);
            }
            else {
                this.amix.filter(o=>o.id==id)[0].active = false;
                this.$emit("amixActive");
            }
        },
        timelineActive(id, active){
            if(active)
                this.timelines.forEach((v)=>{
                    if(v.id == id) {
                        v.active = true;

                        if(this.time_sec < v.offset_sec) {
                            this.time_sec = v.offset_sec;
                        } else if(this.time_sec >= v.offset_sec + v.end_sec - v.start_sec) {
                            // 0.0001time_secvtimeline
                            this.time_sec = v.offset_sec + v.end_sec - v.start_sec - 0.0001;
                        }

                    } else {
                        v.active = false;
                    }
                })
            else 
                this.timelines.find(o=>o.id==id).active = false;
        },
        timelineChange(id, event){
            let found = this.timelines.filter( o=>o.id==id )[0];
            found.start_sec = event.start_sec;
            found.end_sec = event.end_sec;
            this.timelineChanging = !event.end;

            let offset_sec = found.offset_sec;
            if(event.left_drag_offset){
                // timeline item 
                this.timelines.forEach((v)=>{
                    if(v.offset_sec < offset_sec)return;
                    v.left_drag_offset = event.left_drag_offset;
                })
            }
            else{
                this.timelines.forEach((v)=>{
                    v.left_drag_offset = 0;
                })
            }

            // seek
            // if(event.end){
                //seek
                if(event.seek_sec !== undefined){
                    let foundIdx = this.timelines.indexOf(found);

                    // event.seek_sec seekseek
                    let seek_sec = event.seek_sec + 
                        [0, ...this.timelines.slice(0,foundIdx)].reduce((pre,nxt)=>{
                            return pre + nxt.end_sec - nxt.start_sec;
                        });

                    this.time_sec = Math.max(0,seek_sec);
                    this.changed({
                        time_sec: this.time_sec
                    });
                }
                Editlist.editlist.update_timelines(this.timelines);
            // }


            window.console.log("change end:"+event.end)
            if(event.end){
                this.$emit("timelinesChange",this.timelines);
                this.update(true);
            }

            this.update();
            
        },
        currentStart(e) {
            this._pageX = e.changedTouches[0].pageX;
            this._value = this.time_sec;
        },
        currentMove(e) {
            let offset = e.changedTouches[0].pageX - this._pageX;
                
            this.dragging = true;
            //
            if (this._preOffset)
                this._moveSpeed = - offset + this._preOffset;
            this._preOffset = offset;

            this.time_sec = Math.max(0, Math.min( this.total_sec, 
                this._value - offset / this.pps
            ));

            this.changed({
                time_sec: this.time_sec
            })

            return true;
        },
        async changed(opt) {
            let isEnd = !opt;

            if (isEnd && Math.abs(this._moveSpeed) > 1) {

                let speed = this._moveSpeed * 5;
                speed = Math.max(-60, Math.min(60, speed))
                while (Math.abs(speed) > 1) {
                    let newVal = this.time_sec + speed / this.pps;

                    //
                    speed *= 0.8;

                    this.time_sec = Math.max(0, Math.min(this.total_sec,
                        newVal
                    ));

                    await new Promise((o, ) => {
                        clearTimeout(this.timer);
                        this.timer = setTimeout(o, 20);
                    })
                }
            }

            if(isEnd){
                this._preOffset = 0;
                this._moveSpeed = 0;
                this.dragging = false;
                // window.console.log("dragging stop");
            }

            this.$emit("timeChange",{
                time_sec: this.time_sec,
                ...opt
            });
        },
        posterLoadError() {
            this.poster_url = null;
        },
        endingClick() {
            this.$emit('on_ending_click');
        },
        refreshUI() {
            if(this.timelineChanging) {
                return;
            }
            let cur = Editlist.editlist.cur_timeline;
            if(cur) {
                cur.focus = true;
    
                this.timelines.forEach(e => e.id != cur.id && (e.focus = false));
    
                if(!cur.active) {
                    this.timelines.forEach(e => e.active = false);
                }
            }
        }
    },
    mounted() {
        this.update();
        this.updateFrame();
    },
    beforeDestroy() {
        clearInterval(this._goingTmr);
    }
};
</script>
