<template>
    <div HScrollBar ref="bar">
        <span
            v-if="visibleLeftArrow"
            class="material-icons left-icon"
        >
            keyboard_arrow_left
        </span>
        <span
            v-if="visibleRightArrow"
            class="material-icons right-icon"
        >
            keyboard_arrow_right
        </span>
        <slot></slot>
    </div>
</template>
<style scoped>
.left-icon {
    position: fixed;
    z-index: 1;
    margin-top: 5px;
    left: 0;
    color: white;
    opacity: 0.7;
    animation: fade 3s infinite;
    text-shadow: 0 0 3px black;
}
.right-icon {
    position: fixed;
    z-index: 1;
    margin-top: 5px;
    right: 0;
    color: white;
    opacity: 0.7;
    animation: fade 3s infinite;
    text-shadow: 0 0 3px black;
}
@keyframes fade {
    40% {
        opacity: 0.7;
    }
    50% {
        opacity: 0;
    }
    60% {
        opacity: 0.7;
    }
}
</style>
<script>
let MASK_SPAN = 20; //px

export default {
    props: ['scroller'],
    data() {
        return {
            visibleLeftArrow: false,
            visibleRightArrow: false
        };
    },
    watch: {
        scroller: "refresh"
    },
    methods: {
        refresh() {
            let $scroller = this.scroller || this.$el;
            let sLeft = $scroller.scrollLeft;
            let sWidth = $scroller.scrollWidth;
            let width = $scroller.offsetWidth;

            this.visibleLeftArrow = sLeft > MASK_SPAN;
            this.visibleRightArrow = sLeft + width + MASK_SPAN < sWidth;
        }
    },
    beforeDestroy() {
        this.$refs.bar.removeEventListener('scroll', this.onscroll, true);
    },
    mounted() {
        this.$nextTick(this.refresh.bind(this));
        this.onscroll = this.refresh.bind(this);
        this.$refs.bar.addEventListener('scroll', this.onscroll, true);
    }
};
</script>