<template>
<div class="main">
    <PosterMakerTop @close="close" @save="save" :saving="saving" />
    <div class="title">  </div>
    <div ref="mainBox" class="main-box">
        <PreviewPlayer 
            ref="preview" 
            :pause="true" 
            :editlist="editlist" 
            :postermode="true" 
            @render="onPreviewLoaded" 
            class="main-video" />

        <ClipImg ref="overlayEditor" 
            :postermode="true" 
            :edit_filter="e => e.type == Const.Overlay.Type.Text"
            @change="overlayChange"
            @edit="overlayEdit"
            :overlays_in="editlist.poster.overlay"
            :mosics_in="[]"
            :time_sec="editlist.time_sec"
            class="overlay-box" />
    </div>

    <div class="toolBox" >
        <ClipTimeline ref="timeline" 
            :timelines_in="editlist.timelines"
            :playing="!player.pause"
            :postermode="true"
            @timeChange="timeChange" 
            style="" />
        <PosterToolTab  @select="tabSelect"/>
        <ToolDialog :postermode="true" :editlist="editlist" @close="mainBoxResized" @change="toolValueChange" ref="toolDlg"/>
    </div>
</div>
</template>

<style lang="less" scoped>
.title {
    color:white;
    text-align:center;

}
.spin {
    background: rgba(0, 0, 0, 0.8);
    color: white;
}

.main {
    background-color: #333;
    background: transparent;
    position: relative;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: hidden;
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    padding-bottom:20px
}

.main-box {
    background-color: #333;
    background: transparent;
    width: 100%;
    position: relative;
    overflow: hidden;
    flex: auto;
    display: flex;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.main-video {
    width:100%;
    height:100%;
    /* display: flex; */
    /* height: 100%; */
}
.toolBox {
    width: 100%;
    background: rgba(50, 50, 50, 0.9);
    position: relative;

    &>* {
        width: 100%;
    }
}

.overlay-box {
    width:100%;
    height:100%;
    position: absolute;
    left:0;
    top:0;
}

</style>

<script>
import PosterMakerTop from "@/views/Top/PosterMakerTop";
import ClipTimeline from "@/views/Bottom/ClipTimeline";
import ClipImg from "@/views/Editor/ClipImg";
import PreviewPlayer from "@/views/PreviewPlayer";
import PosterToolTab from "@/views/Bottom/PosterToolTab";
import ToolDialog from "@/views/Bottom/ToolDialog";
import ToolTab from "@/views/Bottom/ToolTab";
import Const from "@/const";
import UGCResourceManager from "@/model/UGCResourceManager";

export default {
    props:["editlist"],
    components: {
        PosterMakerTop,
        ClipTimeline,
        ClipImg,
        PreviewPlayer,
        PosterToolTab,
        ToolDialog
    },
    data() {
        return {
            Const,
            player:{
                // style:{
                //     left:0,top:0,width:0,height:0
                // },
                pause:true,
                // time_sec:0
            },
            saving:false,
            waitReload:false,
            importProgress: -1,
            
        };
    },
    methods: {
        async save(){
            if(this.$refs.overlayEditor.hasActive || this.waitReload){
                this.saving = true;
                if(this.$refs.overlayEditor.hasActive) {
                    this.$refs.overlayEditor.blur();
                    if(!this.waitReload)
                        await this.reloadPlayer();
                }
                await new Promise((o,x)=>{
                    let tmr = setInterval(()=>{
                        if(!this.waitReload){
                            clearInterval(tmr);
                            clearTimeout(timeout);
                            o();
                        }
                    },100)
                    let timeout = setTimeout(()=>{
                        clearInterval(tmr);
                        x();
                    },5000)
                });
                this.saving = false;
            }
            
            let dir = await UGCResourceManager.getTempFolder();
            
            // 
            let fileName = "poster_"+Math.random().toString(16).replace("0.", "")+".jpg";

            let des = dir.nativeURL + "/" + fileName;

            try {
                await new Promise(UGCApp.ffplay.thumb.bind(null, des.replace("file://","")));
    
                this.editlist.poster.url = des + "?" + Math.random();
                this.editlist.poster.rotate = 0;
            
                this.$emit("close");
    
                this.editlist.createOperateHistory(Const.Operate.PosterModify.Type);
            } catch(e) {
                this.$mResToast.error(
                    ""
                );
            }
        },
        async close(){
            await new Promise( UGCApp.ffplay.stop );
            this.$emit('close')
        },
        onPreviewLoaded(){
            this.waitReload = false;
        },
        async reloadPlayer(dont_keep_screen){
            this.waitReload = true;
            await this.$refs.preview.reload(!dont_keep_screen);
        },
        timeChange(e) {
            window.console.log("seek:" + e.time_sec)
            this.player.pause = true;
            this.$refs.preview.seek(e.time_sec);
            this.$refs.timeline.seek(e.time_sec);
            this.editlist.time_sec = e.time_sec;
        },
        async overlayChange() {
            await this.reloadPlayer();
        },
        overlayEdit(overlay){
            this.$refs.toolDlg.show(ToolTab.TabID.AddText, {
                height: "auto",
                overlay: overlay,
                operateToolTabId: ToolTab.TabID.ToolSettings
            });
        },
        // overlayActive(e) {
        //     // Editlist.editlist.update_overlay(e);
        // },
        tabSelect(tabID){
            if(tabID == ToolTab.TabID.AddLocalImage) {
                this.pickImg();
            } else {
                this.$refs.toolDlg.show(tabID);
            }
        },
        toolValueChange(data){
            if(data){
                if(data.overlay){
                    data.overlay.offset_sec = 0;
                    data.overlay.end_sec = this.editlist.total_sec;
                    this.$refs.overlayEditor.active(data.overlay);
                    return;
                }
            }
            this.reloadPlayer();
        },
        async pickImg() {
            // native cordova.camera plugin  content:// 
            // CameraLauncher.java::processResultFromGallery::707  fileLocation
            try {
                let file = await new Promise((o, x) =>
                    navigator.camera.getPicture(o, x, {
                        sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                        allowEdit: false,
                        correctOrientation: true,
                        mediaType: Camera.MediaType.PICTURE
                    })
                );

                // window.console.log(file);
                if (!file) {
                    // 
                    this.reloadPlayer();
                    return;
                }

                let overlay = this.editlist.add_overlay({
                    url: file,
                    type: Const.Overlay.Type.Image
                }, true);
                this.toolValueChange({ overlay });
                // 
                this.reloadPlayer();
            } catch (error) {
                // 
                this.reloadPlayer();
            }
        },
        mainBoxResized() {
            this.$nextTick(() => {
                this.$refs.overlayEditor.updateUI();
            });
        }
    },
    async mounted(){
        this.$nextTick(() => this.reloadPlayer(true));
        // this.updatePlayerRect();
        document.addEventListener("backbutton", this.close, false);
    },
    async beforeDestroy() {
        document.removeEventListener("backbutton", this.close, false);
    }

}
</script>