<mu-bottom-sheet
    :overlay-close="editlist.timelines.length > 0"
    :esc-press-close="editlist.timelines.length > 0"
    :open.sync="isopen"
    >
    <mu-list @item-click="close">
      <mu-sub-header></mu-sub-header>
      <mu-list-item button @click="importFile">
        <mu-list-item-action>
          <mu-icon value="folder_open" color="orange"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title></mu-list-item-title>
      </mu-list-item>
<!--    -->
       <mu-list-item button @click="captureFile">
        <mu-list-item-action>
          <mu-icon value="photo_camera" color="orange"></mu-icon>
        </mu-list-item-action>
        <mu-list-item-title></mu-list-item-title>
      </mu-list-item>
    </mu-list>
</mu-bottom-sheet>
</template>

<style scoped>
.dialog-content {
    padding: 24px;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-evenly;
    flex-flow: column;
    height: 200px;
}
</style>

<script>
import Const from "@/const";

export default {
    props: ["editlist"],
    data() {
        return {
            isopen: false
        };
    },
    methods: {
        open() {
            this.isopen = true;
        },
        close() {
            this.isopen = false;
        },
        async importFile(){

            if(this.isIOS()) {
                try{
                    
                    let result = await new Promise(MediaPicker.getMedias.bind(MediaPicker,{
                            selectMode: 102, //101=picker image and video , 100=image , 102=video
                            maxSelectCount: 1 //default 40 (Optional)
                            // 'maxSelectSize': 188743680, //188743680=180M (Optional) only android
                        }
                    ));

                    window.console.log(result);
                    this.$emit("select",result);
                } catch(e){
                    window.console.log(e)
                    // 
                    if(!this.editlist.timelines.length) {
                        this.open();
                    }
                }
            } else {
                try{
                    let result = await new Promise((o, x) => navigator.camera.getPicture(o, x, {
                        // destinationType: Camera.DestinationType.FILE_URI,
                        sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                        allowEdit: true,
                        mediaType: Camera.MediaType.VIDEO
                    }));

                    this.$emit("select",[{
                        uri: result
                    }]);
                }catch(e){
                    window.console.log(e);
                    // 
                    if(!this.editlist.timelines.length) {
                        this.open();
                    }
                }
            }
        },
        async captureFile(){
            try{
                if(this.isAndroid()) {
                    let result = await new Promise((o, x) => 
                        UGCApp.captureVideo(o, x, {
                            duration: Const.CaptureVideo.Duration
                        }));
                    window.console.log(result)
                    if(!result.path) {
                        this.$mResToast.error("");
                        return;
                    }
                    this.$emit("select", [{ uri: result.path }]);
                } else {
                    let result = await new Promise((o, x) => 
                        navigator.device.capture.captureVideo(o, x, {
                            duration: Const.CaptureVideo.Duration
                        }));
                    window.console.log(result)
                    this.$emit("select",result.map(o=>({
                        uri: o.fullPath
                    })));
                }
            }catch(e){
                window.console.log(e);
                if(!this.editlist.timelines.length) {
                    this.open();
                }
            }
        },
        importProgress(progress){
            if(progress.status == "progress"){
                this.$emit("progress",progress.index);
                // window.console.log("import:",progress);
            } 
        }
    },
    mounted(){
        if(this.isIOS()) {
            document.addEventListener("MediaPicker.CompressVideoEvent", this.importProgress);
        }
    },
    beforeDestroy(){
        if(this.isIOS()) {
            document.removeEventListener("MediaPicker.CompressVideoEvent", this.importProgress);
        }
    }
}
</script>
