        get transform_source(){
            return this._transform_source || ""
        },
        set transform_source(v){
            this._transform_source = v;
        },
        mosic:[],// dar * 1000
        mosic_source: null, // 
        add_mosic(o){
            let ret;
            let mosic = this.mosic;
            mosic.push(ret = {
                id:overlayIdSeq++,
                type: Const.Overlay.Type.Mosic,
                new:false,
                x:0,y:0,w:0,h:0,rotate:0,
                active:false,
                offset_sec:Math.max(0,this.time_sec-0.05),
                end_sec:3,
                ...o
            });
            return ret;
        },
        update_mosic(o){
            if (!window.UGCApp) {
                window.console.log("no ugcapp");
                return;
            }

            let found = this.mosic.filter(i=>i.id==o.id);
            if(!found.length)return;
            else found = found[0];

            let scale = this.preview_resolution[0] / this.resolution_dar[0];
            
            UGCApp.ffplay.command(
                "rect|glfilter@mosic"+o.id, [
                    (o.x*scale).toFixed(0),
                    (o.y*scale).toFixed(0),
                    (o.w*scale).toFixed(0),
                    (o.h*scale).toFixed(0) ].join(",")
            );
            UGCApp.ffplay.command(
                "ep|glfilter@mosic"+o.id, (o.end_sec).toFixed(3)
            );
            UGCApp.ffplay.command(
                "offset|glfilter@mosic"+o.id, (o.offset_sec).toFixed(3)
            );
            UGCApp.ffplay.command(
                "percent|glfilter@mosic"+o.id, "100"
            );
        },
