<template>
    <div class="ending-main">
        <PosterMakerTop
            @close="close"
            @save="rendering=true"
        />
        <div class="title"></div>
        <div
            ref="mainBox"
            class="main-box"
        >
            <div style="text-align:center">
                <div
                    class="avatar"
                    @click="onAvatarClick"
                >
                    <span
                        v-if="!avatarUrl"
                        class="material-icons"
                    >account_circle</span>
                    <img
                        :src="avatarUrl"
                        v-else
                        @error="avatarLoadError"
                    />
                    <div class="text"></div>
                </div>
            </div>
            <div class="input">
                <input
                    v-model="ending.nickName"
                    placeholder=""
                    maxlength="18"
                />
            </div>
            <div class="input">
                <input
                    v-model="ending.desc"
                    placeholder=""
                    maxlength="23"
                />
            </div>
        </div>
        <EndingRender
            v-if="rendering"
            ref="render"
            @rendered="save"
            @error="renderError"
            :avatar-file="ending.avatarFile"
            :nick-name="ending.nickName || nickNamePlaceholder"
            :desc="ending.desc"
        />
        <Crop
            v-if="cropping"
            ref="crop"
            @close="cropping = false"
            @save="cropSave"
            :src="avatarUrl"
            :ratio="1"
        />
        <mu-bottom-sheet
            :overlay-close="true"
            :esc-press-close="true"
            :open.sync="isMenuOpen"
        >
            <mu-list @item-click="isMenuOpen=false">
                <mu-sub-header></mu-sub-header>
                <mu-list-item
                    button
                    @click="pickImage"
                >
                    <mu-list-item-action>
                        <mu-icon
                            value="folder_open"
                            color="orange"
                        ></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title style="color:#f44336"></mu-list-item-title>
                </mu-list-item>
                <mu-list-item
                    button
                    @click="deleteAvatar"
                >
                    <mu-list-item-action>
                        <mu-icon
                            value="delete"
                            color="orange"
                        ></mu-icon>
                    </mu-list-item-action>
                    <mu-list-item-title></mu-list-item-title>
                </mu-list-item>
            </mu-list>
        </mu-bottom-sheet>
    </div>
</template>

<style lang="less" scoped>
.title {
    color: white;
    text-align: center;
}

.ending-main {
    background-color: #333;
    position: relative;
    width: 100%;
    height: 100%;
    padding-bottom: 20px;
}

.avatar {
    margin-top: 30px;
    text-align: center;
    overflow: hidden;
    display: inline-block;

    .material-icons {
        font-size: 80px;
        color: #555;
    }

    img {
        border-radius: 50%;
        width: 80px;
        height: 80px;
    }
    .text {
        margin-top: 10px;
        text-align: center;
        color: #999;
    }
}

.input {
    padding: 20px;

    input {
        line-height: 1;
        width: 100%;
        padding: 12px 12px;
        border-radius: 6px;
        background: #444;
        border: 0;
        color: #bbb;
    }
}
</style>

<script>
import PosterMakerTop from "@/views/Top/PosterMakerTop";
import Crop from "@/components/Crop";
import Editlist from "@/model/Editlist";
import UGCResourceManager from "@/model/UGCResourceManager";
import ClipModel from "@/model/ClipModel";
import Const from "@/const";
import EndingRender from "@/views/Editor/EndingRender";

export default {
    components: {
        PosterMakerTop,
        EndingRender,
        Crop
    },
    data() {
        return {
            rendering: false,
            editlist: null,
            avatarUrl: null,
            isMenuOpen: false,
            cropping: false,
            nickNamePlaceholder: "",
            ending: {
                avatarFile: "",
                nickName: "",
                desc: ""
            }
        };
    },
    methods: {
        avatarLoadError() {
            this.avatarUrl = "";
        },
        onAvatarClick() {
            if (this.ending.avatarFile) {
                this.isMenuOpen = true;
            } else {
                this.pickImage();
            }
        },
        deleteAvatar() {
            this.ending.avatarFile = '';
            this.ending.url = '';
            this.avatarUrl = '';
        },
        async pickImage() {
            // native cordova.camera plugin  content:// 
            // CameraLauncher.java::processResultFromGallery::707  fileLocation
            let file = await new Promise((o, x) =>
                navigator.camera.getPicture(o, x, {
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    allowEdit: false,
                    mediaType: Camera.MediaType.PICTURE,
                    destinationType: Camera.DestinationType.NATIVE_URI
                })
            );

            if (!file) return;

            this.ending.avatarFile = file;
            this.loadImage();
            this.cropping = true;
        },
        async save() {
            this.rendering = false;

            let ending = {
                avatarFile: this.ending.avatarFile,
                nickName: this.ending.nickName,
                desc: this.ending.desc,
                url: null
            };
            UGCResourceManager.saveLastEnding(ending);

            let data = await this.$refs.render.getBlob();
            let dir = await UGCResourceManager.getTempFolder();
            let fileName =
                dir.name + "/endingImage_" +
                Math.random()
                    .toString(16)
                    .replace("0.", "") +
                ".png";

            let outFile = await this.createCacheFile(data, fileName);
            ending.url = outFile;
            window.console.log("created ending file: " + outFile);

            let curEnding = Editlist.editlist.timelines.find(e => e.ending);
            if (curEnding) {
                Object.assign(curEnding, ending);
            } else {
                Editlist.editlist.add_timelines([
                    {
                        start_sec: 0,
                        end_sec: 2,
                        total_sec: 2,
                        ending: true,
                        ...ending
                    }
                ]);
            }

            this.$emit("close");

            Editlist.editlist.createOperateHistory(
                Const.Operate.EndingModify.Type
            );
        },
        async renderError(error) {
            this.rendering = false;
            this.$mResToast.error("");
            window.console.error(error);
        },
        async close() {
            await new Promise(UGCApp.ffplay.stop);
            this.$emit("close");
        },
        async loadImage() {
            if (!this.ending || !this.ending.avatarFile) {
                return;
            }

            let url = "";
            try {
                url = await ClipModel.Util.getCdvUrl(this.ending.avatarFile);
                if (location.protocol != "cdvfile:") {
                    url = await UGCResourceManager.assetReadAsDataUrl(url);
                }
            } catch (error) {
                url = this.ending.avatarFile; // PC
            }
            this.avatarUrl = url;
        },
        async cropSave(data) {
            let dir = await UGCResourceManager.getTempFolder() ;
            let fileName =
                dir.name + "/avatarCropImage_" +
                Math.random()
                    .toString(16)
                    .replace("0.", "") +
                ".png";

            let outFile = await this.createCacheFile(data, fileName);
            this.ending.avatarFile = outFile;
            await this.loadImage();
            this.cropping = false;
        },
        async createCacheFile(data, output) {
            if (!window.requestFileSystem) {
                // PC
                return URL.createObjectURL(data);
            }

            let fs = await UGCResourceManager.filesystem();
            let file = await new Promise((o, x) => {
                fs.root.getFile(
                    output,
                    { create: true, exclusive: false },
                    o,
                    x
                );
            });
            let writer = await new Promise((o, x) => {
                file.createWriter(o, x);
            });

            await new Promise((o, x) => {
                writer.onwriteend = o;
                writer.onerror = x;
                writer.write(data);
            });

            return file.nativeURL;
        }
    },
    async mounted() {
        let curEnding = Editlist.editlist.timelines.find(e => e.ending);
        if (!curEnding) {
            curEnding = UGCResourceManager.getLastEnding();
        }
        if (curEnding) {
            this.ending = { ...curEnding };
            this.loadImage();
        }
        document.addEventListener("backbutton", this.close, false);
    },
    async beforeDestroy() {
        document.removeEventListener("backbutton", this.close, false);
    }
};
</script>