import cordovaModel from "@/model/CordovaModel"

let THIS = {
    Util:{
        async getCdvUrl(i) {
            if(i.indexOf("http://localhost") == 0){
                i = i.replace(/http:\/\/localhost:.*?\//,"cdvfile://localhost/")
            }
            if(i.indexOf("/") == 0){
                i = "file://"+i;
            }
            return await new Promise((o, x) =>
                resolveLocalFileSystemURL(
                    i,
                    function(entry) {
                        o(entry.toInternalURL());
                    },
                    () => {
                        x();
                    }
                )
            );
        },
        async getLocalUrl(i) {
            if(i.indexOf("http://localhost") == 0){
                i = i.replace(/http:\/\/localhost:.*?\//,"cdvfile://localhost/")
            }
            if(i.indexOf("/") == 0){
                i = "file://"+i;
            }
            return await new Promise((o, x) =>
                resolveLocalFileSystemURL(
                    i,
                    function(entry) {
                        // urlencode 
                        o(THIS.Util.normalizeFileUrl(entry.toURL()));
                    },
                    () => {
                        x();
                    }
                )
            );
        },
        normalizeFileUrl(url) {

            if(url.toLowerCase().search(/.{0,5}file:\/\//) == 0){
                //urlencodeffmpeg
                url = decodeURI( url );
            }

            return url;
        }
    }, 
    async getThumbList(src,output){
        let localSrc = await Promise.all(src.map(async (i)=>{
            return await THIS.Util.getLocalUrl(i);
        }));
        localSrc;
        // console.log("get format from:"+localSrc)
        let format = await Promise.all(src.map(async (i)=>{
            return await new Promise((o, x) =>
                UGCApp.ffmpeg.format(
                    i,o,x
                )
            );
        }));
        format;

        // console.log(format)
        let thumbPath = src.map((_,idx)=>{
            return cordova.file.cacheDirectory + "editor_thumb_"+idx+".jpg";
        });

        let input = ""
        let out = ""
        let filter = " -filter_complex '"
        for(let i=0; i<src.length; i++){
            input += " -analyzeduration 0 -probesize 32 -skip_frame nokey -t 0.001 -i '"+src[i]+"' ";
            filter += "["+i+":v]scale=100:100:force_original_aspect_ratio=increase,crop=100:100[out"+i+"];";
            out += " -map [out"+i+"] -frames:v 1 -f image2 -y '"+thumbPath[i]+"'";
        }

        filter = (filter).replace(/;$/,"'");

        UGCApp.ffmpeg.start(
            "ffmpeg "+input+filter+out
        );

        // let progress = 0;
        let status = 0;

        output.progress = -1;
        do {
            output.status = status = await new Promise((o, x) =>
                UGCApp.ffmpeg.status( o,x )
            );
            await new Promise((o, ) => {
                setTimeout(o, 1000);
            });
        } while (status == 1);

        return await Promise.all(thumbPath.map(async (i)=>{
            let cdvurl = await THIS.Util.getCdvUrl(i);
            let url = Url(cdvurl,true);
            url.query["_"] = Math.random();
            return url.toString();
        }));
    },
    async getThumb(src,output){
        let localSrc = await THIS.Util.getLocalUrl(src);

        // console.log("get format from:"+localSrc)
        let format = await new Promise((o, x) =>
            UGCApp.ffmpeg.format(
                localSrc,o,x
            )
        );

        // console.log(format)
        let thumbPath = cordova.file.cacheDirectory + "editor_thumb.jpg";
        await new Promise((o, ) =>
            resolveLocalFileSystemURI(thumbPath, e => (e.remove(), o()), o)
        ).catch(()=>{});

        let thumbCount = 10;
        let input = ""
        let concat = "";
        for(let i=0; i<thumbCount; i++){
            let ss = format.duration/1000 / thumbCount * i;
            input += " -analyzeduration 0 -probesize 32 -ss "+ss+" -t 0.001 -i '"+localSrc+"' ";
            concat += "["+i+"]";
        }
        concat += "concat="+thumbCount;
        let cmd = "ffmpeg {input} -filter_complex {concat},scale=100:100:force_original_aspect_ratio=increase,crop=100:100,tile={n}x1 -f image2 -y {des}"
            .replace("{input}", input)
            .replace("{des}", thumbPath)
            .replace("{concat}", concat)
            .replace(/\{n\}/g, thumbCount);

        // console.log(cmd);return;
        UGCApp.ffmpeg.start(
            cmd
        );

        // let progress = 0;
        let status = 0;

        output.progress = -1;
        do {
            output.status = status = await new Promise((o, x) =>
                UGCApp.ffmpeg.status( o,x )
            );
            await new Promise((o, ) => {
                setTimeout(o, 1000);
            });
        } while (status == 1);

        return await THIS.Util.getCdvUrl(thumbPath);
    },
    Transcode: {
        // opt:{
        //     imgs:[{
        //         url:"",
        //         x:0,
        //         y:0,
        //         w:0,
        //         h:0,
        //         rotate:0
        //     }],
        //     timeline:{
        //         left: 0,
        //         right: 0
        //     }
        // }
        //     output:{
        //         progress:0,
        //         status:0,
        //         desSrc:""
        //     }
        // 
        async singleEx(src, opt, output) {
            let ret = false;
            ret = await THIS.Transcode.single(src,opt,output);
            if(!ret){
                ret = await THIS.Transcode.single(src,opt,output,true);
            }
            return ret;
        },
        async single(src, opt, output, forceSoftwareEncoder) {
            let localSrc = src.indexOf("file://")==0?src:
                (await THIS.Util.getLocalUrl(src));

            let format = await new Promise((o, x) =>
                UGCApp.ffmpeg.format( localSrc,o,x )
            );

            let pOverlay = "";
            let pIn = "0:v";
            let pOut;

            // 90
            if ((90 + format.video.rotate) % 180 == 0) {
                let _cacheWidth = format.video.width;
                format.video.width = format.video.height;
                format.video.height = _cacheWidth;
            }

            // ui
            let videoScale = Math.max(
                format.video.width / opt.uiBox.width,
                format.video.height / opt.uiBox.height
            );

            // ui
            let offsetX =
                (opt.uiBox.width * videoScale) / 2 -
                format.video.width / 2;
            let offsetY =
                (opt.uiBox.height * videoScale) / 2 -
                format.video.height / 2;

            for (let idx in opt.imgs) {
                let i = opt.imgs[idx];
                let item =
                    "movie='{url}'{gif},scale={w}:{h},rotate='a={rotate}*PI/180:ow=hypot(iw,ih):oh=ow:c=none'[p{id}],[{in}][p{id}]overlay={x}:{y}{shortest}[{out}]";
                //rotate
                let rotatedSize = Math.sqrt(i.w * i.w + i.h * i.h);
                let isGif = i.url.toLowerCase().lastIndexOf(".gif")==i.url.length-4;
                item = item
                    .replace("{gif}",
                        isGif?":loop=0:discontinuity=1,setpts=N/(FR*TB)":"")
                    .replace("{shortest}",isGif?":shortest=1":"")
                    .replace(
                        "{url}",
                        i.url.indexOf("data:") == 0 ? i.url.replace(":","\\\\:") :
                        (await THIS.Util.getLocalUrl(i.url)).replace("file://", "").replace(/:/g, "\\:")
                    )
                    .replace("{w}", (i.w * videoScale).toFixed(2))
                    .replace("{h}", (i.h * videoScale).toFixed(2))
                    //rotate
                    .replace(
                        "{x}",
                        (
                            (i.x - (rotatedSize / 2 - i.w / 2)) * videoScale -
                            offsetX
                        ).toFixed(2)
                    )
                    .replace(
                        "{y}",
                        (
                            (i.y - (rotatedSize / 2 - i.h / 2)) * videoScale -
                            offsetY
                        ).toFixed(2)
                    )
                    .replace("{rotate}", i.rotate)
                    .replace(/\{id\}/g, i.id)
                    .replace("{in}", pIn)
                    .replace("{out}", "outP" + i.id);
                pIn = pOut = "outP" + i.id;
                pOverlay && (pOverlay += ",");
                pOverlay += item;
            }

            // tbr 120 120fps
            let fps = "fps=30";
            // 3840x2160h5
            //  2304x1080(rotate 90) 1080x23042160
            let aspectLimit4k = "scale='min(1080,iw)':min'(1080,ih)':force_original_aspect_ratio=decrease,scale=trunc(iw/2)*2:trunc(ih/2)*2";

            let vfilter = '-vf "';
            if(pOverlay)
                vfilter += pOverlay.replace("[" + pOut + "]", "") + ",";
            vfilter += fps + ",";
            vfilter += aspectLimit4k;
            if(opt.vfilter)
                vfilter += ","+opt.vfilter;
            vfilter += '"';

            let afilter = ""
            if(opt.afilter){
                afilter = '-af "';
                afilter += opt.afilter + "\"";
            }

            let desSrc = localSrc + "-" + new Date().getTime() + ".mp4";
            let bitrate = Math.pow(Math.max(format.video.width, format.video.height) / 1280, 2) * 3000;
            let vcodec = "h264";
            // let vdcodec = "h264";
            if (cordovaModel.isAndroid() && !forceSoftwareEncoder){
                vcodec = "libhw_avc";
                //  h264_mediacodec 
                // vdcodec = "h264_mediacodec";
            }
            if (cordovaModel.isIOS()) {
                vcodec = "h264_videotoolbox";
                //  videotoolbox 
                // vdcodec = "h264 -hwaccel videotoolbox";
                desSrc = cordova.file.cacheDirectory + "output.mp4";
            }
            UGCApp.ffmpeg.start(
                "ffmpeg -accurate_seek {start} -t {len} -i {src} {vfilter} {afilter} -acodec aac -b:v {b:v} -vcodec {vcodec} -use_editlist 0 -f mp4 -y {des}"
                    .replace("{start}", opt.timeline.left>1?
                        ("-ss "+opt.timeline.left)
                        :"")
                    .replace(
                        "{len}",
                        opt.timeline.right - opt.timeline.left
                    )
                    // .replace("{vdcodec}", vdcodec)
                    .replace("{vcodec}", vcodec)
                    .replace("{src}", localSrc)
                    .replace("{vfilter}", vfilter)
                    .replace("{afilter}", afilter)
                    .replace("{b:v}", bitrate + "k")
                    .replace("{des}", desSrc)
            );
            let newDuration =
                1000 * (opt.timeline.right - opt.timeline.left);
            let progress = 0;
            let status = 0;

            await new Promise((o, ) => {
                setTimeout(o, 1000);
            });
            do {
                output.status = status = await new Promise(
                    UGCApp.ffmpeg.status
                );
                progress = await new Promise(UGCApp.ffmpeg.progress);
                output.progress = Math.max(0,progress.pts / newDuration);

                await new Promise((o, ) => {
                    setTimeout(o, 200);
                });
                window.console.log(
                    "ffmpeg progress:" + progress.pts + ", status:" + status
                );
            } while (status == 1);

            let result = await new Promise(UGCApp.ffmpeg.report);

            if (result == 0) {
                if (cordovaModel.isIOS()) {
                    let dcimDesSrc = await new Promise((o, x) => UGCApp.sendToDCIM(desSrc, o, x));
                    output.desSrc = await THIS.Util.getLocalUrl(dcimDesSrc);
                }
                else {
                    UGCApp.scanFile(desSrc);
                    output.desSrc = await THIS.Util.getCdvUrl(desSrc);
                }
                // THIS.$nextTick(() => {
                //     THIS.$refs.video.load();
                // })
            } else {
                // THIS.$Message.error({
                //     content: ": " + result,
                //     duration: 3
                // });
            }

            return result == 0;
        },
        async mergeEx(videos, opt, output){

            let ret = false;
            ret = await THIS.Transcode.merge(videos,opt,output);
            if(!ret){
                ret = await THIS.Transcode.merge(videos,opt,output,true);
            }
            return ret;

        },
        async merge(videos, opt, output, forceSoftwareEncoder){
            let localSrc = await Promise.all(videos.map(async (src)=>{
                return await THIS.Util.getLocalUrl(src);
            }));

            let input = "";
            let scale = "";
            let concat = "";

            opt.renderParams.map((param,idx)=>{
                input += " -i '"+localSrc[idx]+"' "
                scale += "[{idx}]scale={w}:{h},pad={boxW}:{boxH}:{x}:{y},setsar=sar=1/1,setdar=dar={boxW}/{boxH}[{idx}_out];"
                    .replace(/{idx}/g,idx)
                    .replace('{w}',param.w)
                    .replace('{h}',param.h)
                    .replace('{x}',param.x)
                    .replace('{y}',param.y)
                    .replace(/{boxW}/g,opt.width)
                    .replace(/{boxH}/g,opt.height)

                concat += "[{idx}_out][{idx}:a]"
                    .replace(/{idx}/g,idx)

                if(!param.hasAudio) {
                    scale += "anullsrc,atrim=duration={duration}[{idx}:a];"
                        .replace("{duration}",param.duration)
                        .replace(/{idx}/g,idx)
                }
            })

            let desSrc = localSrc[0] + "-" + new Date().getTime() + ".mp4";
            let bitrate = Math.pow(Math.max(opt.width, opt.height) / 1280, 2) * 3000;
            let vcodec = "h264";
            if (cordovaModel.isAndroid() && !forceSoftwareEncoder) vcodec = "libhw_avc";
            if (cordovaModel.isIOS()) {
                vcodec = "h264_videotoolbox";
                desSrc = cordova.file.cacheDirectory + "output.mp4";
            }

            //single
            let fps = "fps=30"
            let aspectLimit4k = "scale='min(3840,iw)':min'(2160,ih)':force_original_aspect_ratio=decrease,scale=trunc(iw/2)*2:trunc(ih/2)*2";

            let cmd = 
                "ffmpeg {input} -filter_complex \"{scale}{concat}concat=n={concat_n}:v=1:a=1[outv][outa];[outv]{fps},{limit}[outv2]\" -map [outa] -map [outv2] -acodec aac -b:v {b:v} -vcodec {vcodec} -use_editlist 0 -f mp4 {des}"
                    .replace("{input}", input)
                    .replace( "{scale}", scale)
                    .replace( "{fps}", fps)
                    .replace( "{limit}", aspectLimit4k)
                    .replace( "{concat}", concat)
                    .replace( "{concat_n}", videos.length)
                    .replace("{vcodec}", vcodec)
                    .replace("{b:v}", bitrate + "k")
                    .replace("{des}", desSrc)
                    
            // console.log(cmd);
            // return;
            UGCApp.ffmpeg.start(cmd);


            let newDuration = opt.renderParams.reduce((p,c)=>{
                return {duration:p.duration+c.duration}
            }).duration * 1000;
            
            let progress = 0;
            let status = 0;

            do {
                output.status = status = await new Promise(
                    UGCApp.ffmpeg.status
                );
                progress = await new Promise(UGCApp.ffmpeg.progress);
                output.progress = progress.pts / newDuration;

                await new Promise((o, ) => {
                    setTimeout(o, 200);
                });
                // console.log(
                //     "ffmpeg progress:" + progress.pts + ", status:" + status
                // );
            } while (status == 1);

            let result = await new Promise(UGCApp.ffmpeg.report);
            if (result == 0) {
                if (cordovaModel.isIOS()) {
                    let dcimDesSrc = await new Promise((o, x) => UGCApp.sendToDCIM(desSrc, o, x));
                    output.desSrc = await THIS.Util.getLocalUrl(dcimDesSrc);
                }
                else {
                    UGCApp.scanFile(desSrc);
                    output.desSrc = await THIS.Util.getCdvUrl(desSrc);
                }
            }

            return result == 0;

        },
        cancel(){
            UGCApp.ffmpeg.cancel();
        }
    }
}

export default THIS;