<template>
    <div class="audio-base-main" @touchend="touchend">
        <span style="color:white;width:100px">({{audio.volume}})</span>
        <mu-slider style="margin:0" :min="0" :max="300" :step="1" v-model="audio.volume" />
    </div>
</template>
<style scoped>
.audio-base-main{
    display:flex;
}
</style>
<script>
import Editlist from "@/model/Editlist";
import Const from "@/const";

export default {
    props:["title"],
    data(){
        return {
            audio:{volume:100}
        }
    },
    methods:{
        touchend(){
            this.$emit('change')
        },
        onDialogClose(){
            if(this.initState) {
                if(this.initState.volume != this.audio.volume) {
                    Editlist.editlist.createOperateHistory(Const.Operate.AudioParamsModify.Type);
                }
            }
        }
    },
    mounted(){
        this.audio = Editlist.editlist.audio;
        this.$emit("update:title", "");
        this.initState = {
            volume: this.audio.volume
        };
    }
}
</script>