<template>
    <div class="main-title">
        <mu-button large icon @click="exit" >
            <mu-icon color="white" size="36" value="chevron_left" />
        </mu-button>
        <div style="width:100%;text-align:center"></div>
        <!-- <div class="title-text" style="width:100%;"></div> -->

        <mu-button ref="showDlgBtn" @click="showDlg" icon large >
            <mu-icon :color="setting?'red':'white'" size="24" value="settings" />
        </mu-button>
        <mu-button @click="$emit('export')" color="red" small round>
            
        </mu-button>
        <transition name="fade">
        <div ref="dlg" v-show="setting" tabindex="-1" @blur="blur" class="settings-dlg">
            <div class="content">
                <div class="setting-title"></div>
                <div class="content-top">
                    <div @click="encodeparam.preset='high'" :class="{active:encodeparam.preset=='high'}" class="content-item">
                        
                        <div class="content-desc"></div>
                        <div class="content-resolution"> {{high_resolution}}</div>
                    </div>
                    <div @click="encodeparam.preset='low'" :class="{active:encodeparam.preset=='low'}" class="content-item">
                        
                        <div class="content-desc"></div>
                        <div class="content-resolution"> {{low_resolution}}</div>
                    </div>
                    
                </div>
                <div class="content-other">
                     <mu-switch @change="focus" v-model="encodeparam.hardware_encoder"></mu-switch>
                </div>
            </div>
        </div>
        </transition>
    </div>
</template>
<style lang="less" scoped>
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.main-title{
    display: flex;
    color:white;
    align-items: center;
    padding-right:20px
}
.title-text{
    position:absolute;
    width:100%;height:100%;
    text-align: center;
    
}

.settings-dlg{
    width:100%;
    top:45px;
    position: absolute;
    padding:10px 0;
    outline: 0;
    z-index:100;
    overflow:hidden;
    .content{
        background:#272727;
        border-top: 1px solid;
        border-image: linear-gradient(90deg, transparent,rgba(255,0,0,0.4), transparent) 1 10;
        box-shadow: 0px 0px 5px #000;
        border-radius:3px;
        .setting-title{
            padding:10px;
            text-align: center;
            border-bottom:1px solid #444;
        }
        .content-top{
            display:flex;
            justify-content: center;
            .content-item{
                text-align: center;
                padding: 10px;
                background: #444;
                margin: 10px;
                border-radius: 3px;
                border:1px solid transparent;
            }
            .content-item.active{
                border:1px solid red;
            }
            .content-desc{
                font-size: 10px;
                color: #999;
            }
            .content-resolution{
                display: inline-block;
                padding: 0px 10px;
                border-radius: 10px;
                font-size: 10px;
                background: #333;
            }
        }
        .content-other{
            border-top:1px solid #111;
            padding:10px 20px;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }
}
</style>
<script>
import Editlist from "@/model/Editlist";

export default {
    data(){
        return {
            setting: false,
            encodeparam: Editlist.editlist.encodeparam,
            low_resolution: "",
            high_resolution: ""
        }
    },
    methods:{
        exit(){
            // if(UGCApp && UGCApp.exit)
            //     UGCApp.exit(); //ios
            // else
            //     navigator.app.exitApp(); //android
            UGCApp.done({
                statusCode: UGCApp.StatusCode.Cancel,
                message: "",
                output: "",
                output_thumb: ""
            })
        },
        updateData(){
            this.encodeparam = Editlist.editlist.encodeparam;
            this.low_resolution = Editlist.editlist.low_encodeparam.resolution.join("x");
            this.high_resolution = Editlist.editlist.high_encodeparam.resolution.join("x");
        },
        showDlg(){
            if(!this.setting){
                this.updateData();
                this.setting = true;
                this.$nextTick(()=>{
                    this.$refs.dlg.focus()
                })
            }
            else
                this.setting = false;
        },
        focus(){
            this.$refs.dlg.focus()
        },
        blur(e){
            if( e.relatedTarget != this.$refs.showDlgBtn.$el &&
                !this.$refs.dlg.contains(e.relatedTarget)){
                this.setting = false;
            }
        }
    }
}
</script>