<template>
    <div class="box" :class="{active:active, focus:focus, span:offset_sec}" :style="boxStyle" 
        @touchend="toggleActive">

        <mu-button flat class="left-tick" ref="left" 
            @touchstart.stop.native="leftStart" 
            @touchmove.stop.native="leftMove"
            @touchend.stop.native="leftEnd">
            <mu-icon :class="{ dragging: leftDragging, outbound: leftOutBound }" value="keyboard_arrow_left" />
        </mu-button>
        <div class="content">
            <div class="thumb" :class="{overlay:overlay_mode}" :style="thumbStyle">
                <slot></slot>
            </div>
            <div class="border"></div>
        </div>
        <mu-button class="right-tick" ref="right" 
            @touchstart.stop.native="rightStart" 
            @touchmove.stop.native="rightMove" 
            @touchend.stop.native="rightEnd">
            <mu-icon :class="{ dragging: rightDragging, outbound: rightOutBound }" value="keyboard_arrow_right" />
        </mu-button>

        <div class="left-connect" v-if="!active">
            <mu-icon :size="12" value="insert_link" />
        </div>
    </div>
</template>
<style scoped>
.box{
    height:100%;
    position:absolute;
}
.box.active{
    z-index:100;
}
.box.active .border,
.box.focus .border{
    border: 2px #fff solid;
    position: absolute;
    left:0;right:0;
    top: 0;bottom: 0;
}
.box.span{
    border-left:5px solid transparent;
}

.box.span .thumb{
    margin-left:-5px;
} 
.box.span .left-connect{
    display: flex;
} 

.left-connect{
    display: none;
    align-items: center;
    justify-content: center;
    /* border: 1px solid black; */
    color: gray;
    position: absolute;
    left: -10px;
    top: 50%;
    margin-top: -7.5px;
    width: 15px;
    height: 15px;
    border-radius: 3px;
    background: white;
}
.content{
    width:100%;
    height:100%;
    overflow:hidden;
}
.thumb{
    height:100%;
    position:relative;
    /* background:linear-gradient(90deg, green 0%,red 100%); */
    background: black;
}
.thumb.overlay{
    background:red;
}
.left-tick {
    background-color: white;
    width: 20px;
    position: absolute;
    height: 100%;
    top: 0;
    left: -20px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 5px;
    z-index: 1;
    min-width: auto;
}

.left-tick>* {
    font-size: 30px
}


.right-tick {
    background-color: white;
    width: 20px;
    position: absolute;
    height: 100%;
    top: 0;
    right: -20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 5px;
    z-index: 2;
    min-width: auto;
}

.right-tick>* {
    font-size: 40px
}

/*  */
.left-tick::before,
.right-tick::before {
    display: block;
    content: "";
    left: -10px;
    height: 100%;
    position: absolute;
    right: -10px;
}

.box .left-tick{
    display: none
}
.box .right-tick{
    display: none
}
.box.active .left-tick{
    display: flex
}
.box.active .right-tick{
    display: flex
}
.box.active .content {
    border-color: white;
}
.box .dragging {
    color: orange;
}
.box .outbound {
    color: orangered;
}
</style>
<script>
export default {
    props:[
        "pps",
        "dragging",
        "active",
        "focus",
        "overlay_mode",
        "start_sec",
        "end_sec",
        "total_sec",
        "offset_sec",
        "min_sec",
        "left_drag_offset"//
    ],
    data(){
        return {
            // total_sec:0,
            // start_sec:0,
            // end_sec:0,
            // offset_sec:0,
            // active:false,
            // left_drag_offset:0, 
            leftOutBound: false,
            rightOutBound: false,
            leftDragging: false,
            rightDragging: false,
            boxStyle:{
                width:0,
                left:0
            },
            thumbStyle:{
                width:0,
                left:0
            }
        }
    },
    watch:{
        start_sec:"update",
        total_sec:"update",
        end_sec:"update",
        offset_sec:"update",
        left_drag_offset:"update"
    },
    methods:{
        update(){
            if(this.end_sec <= this.start_sec)
                this.end_sec = this.start_sec
            this.boxStyle.width = (this.end_sec - this.start_sec) * this.pps + "px";
            this.boxStyle.left = (this.left_drag_offset + this.offset_sec) * this.pps + "px";
            this.thumbStyle.width = this.total_sec * this.pps + "px";
            this.thumbStyle.left = this.start_sec * this.pps * -1 + "px";
        },
        toggleActive(){
            // touchend  dragging 
            // click  touchend dragging  false
            if(this.dragging)return;
            this.$emit("active",!this.active)
        },
        leftStart(e) {
            this._pageX = e.changedTouches[0].pageX; 
            this._value = this.start_sec; 
            this.leftDragging = true;
            this.leftMove(e)
        },
        leftMove(e) {

            let offset_sec = 
                (e.changedTouches[0].pageX - this._pageX) / this.pps;

            let start_sec = Math.min(this.end_sec - this.min_sec, 
                Math.max(0, this._value + offset_sec)
            );

            if(start_sec == this.end_sec - this.min_sec
                || start_sec == 0) {
                this.leftOutBound = true;
            } else {
                this.leftOutBound = false;
            }

            this.changed({
                seek_sec: 0,
                start_sec,
                left_drag_offset: start_sec - this._value
            });
            return true;
        },
        leftEnd() {
            this.leftDragging = false;
            this.leftOutBound = false;
            this.changed();
        },
        rightStart(e) {
            this._pageX = e.changedTouches[0].pageX; 
            this._value = this.end_sec; 
            this.rightDragging = true;
            this.rightMove(e)
        },
        rightMove(e) {
            let offset_sec = (e.changedTouches[0].pageX - this._pageX) / this.pps;
            
            let end_sec = Math.min(this.total_sec, 
                    Math.max(this.start_sec+this.min_sec, this._value + offset_sec)
                );

            if(end_sec == this.start_sec+this.min_sec
                || end_sec == this.total_sec) {
                this.rightOutBound = true;
            } else {
                this.rightOutBound = false;
            }

            this.changed({
                seek_sec: end_sec - this.start_sec,
                end_sec
            });
            return true;
        },
        rightEnd() {
            this.rightDragging = false;
            this.rightOutBound = false;
            this.changed();
        },
        changed(opt){
            let end = !opt;
            if(!opt)
                opt = {
                    left_drag_offset:0
                }

            this.$emit("change",{
                end,
                start_sec: this.start_sec,
                end_sec: this.end_sec,
                left_drag_offset: this.left_drag_offset,
                ...opt
            })
        }
    },
    mounted(){
        this.update();
        if(!this.pps){
            this.pps = screen.width / 10;
        }
    }
}
</script>