<template>
    <div tabindex="-1" class="box" :class="{active:active,amix:is_amix}"
        :style="[boxStyle, {height: active ? '100%' : '0'}]"
        @click.stop="toggleActive">
        <div class="left-icon">
            <mu-button fab small :color="is_amix?'orange':'red'" > 
                <mu-icon color="white" size="20" :value="typeIcon" /> 
            </mu-button>
        </div>
        <div class="left-drag"
            @touchstart.stop="dragStart"
            @touchmove.stop="dragMove"
            @touchend.stop="dragEnd()">
            <mu-button fab small color="white" textColor="red"> <mu-icon size="20" value="code" /> </mu-button>
        </div>
        <mu-button class="left-tick" ref="left"
            flat
            @touchstart.stop.native="leftStart"
            @touchmove.stop.native="leftMove"
            @touchend.stop.native="changed()">
            <mu-icon :class="{ dragging: leftDragging }" value="keyboard_arrow_left" />
        </mu-button>
        <div class="content">
            <div class="thumb overlay">
                <mu-icon :value="typeIcon" />
                <span>{{typeText}}</span>
                <span class="time">{{fmtSeconds(end_sec)}}</span>
            </div>
        </div>
        <mu-button class="right-tick" ref="right" 
            @touchstart.stop.native="rightStart"
            @touchmove.stop.native="rightMove"
            @touchend.stop.native="changed()">
            <mu-icon :class="{ dragging: rightDragging }" value="keyboard_arrow_right" />
        </mu-button>
    </div>
</template>
<style lang="less" scoped>
