    height: 0;
    position: absolute;
    outline: 0;
}
.box.active {
    z-index: 100;

    .content {
        border: 0;
        height: 100%;
    }
    .left-drag {
        display: block;
    }

    .left-icon {
        display: none;
    }
    .left-tick {
        height: 100%;
        min-width: auto;
    }
    .right-tick {
        height: 100%;
        min-width: auto;
    }
}
.box.span {
    border-left: 5px solid transparent;
}

.box.span .thumb {
    margin-left: -5px;
}

.box .time {
    margin-left: 0.5em;
    padding-left: 0.25em;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
}

.left-icon {
    position: absolute;
    left: -15px;
    margin-top: 14px;
    button {
        width:30px;height:30px;
        // border-radius:0;
        border-top-right-radius: 1px;
        transform:rotate(-45deg)
    }
}

