    border-color: orange;
}
.thumb {
    height: 100%;
    position: relative;
    background: linear-gradient(90deg, green 0%, red 100%);
}
.thumb.overlay {
    width: 100%;
    background: red;
    color: #eee;
    display: flex;
    align-items: center;
    padding: 0 10px;
    font-size: 12px;
}

.amix .thumb.overlay {
    background: orange;
}
.left-tick {
    background-color: white;
    width: 20px;
    position: absolute;
    height: 100%;
    top: 0;
    left: -20px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 5px;
    overflow: hidden;
    transition: all 0.3s;
    height: 0;
}

.left-tick > * {
    font-size: 30px;
}

.right-tick {
    background-color: white;
    width: 20px;
    position: absolute;
    height: 100%;
    top: 0;
    right: -20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 0px 5px;
    overflow: hidden;
    transition: all 0.3s;
    height: 0;
}

.right-tick > * {
    font-size: 30px;
}

