<template>
    <div class="box">
        <img v-for="(i,idx) in thumbs" :key="idx" 
            :src="i.url || firstThumb"
            :style="{left:i.ss*pps+'px', width:i.duration*pps+'px'}" @error="thumbLoadError(idx)"/>
    </div>
</template>
<style scoped>
.box{
    position:relative;
}
.box img{
    position:absolute;
}
</style>
<script>
const DURATION = 10
const SPAN = 1;

import clipModel from "@/model/ClipModel.js";
import Editlist from "@/model/Editlist";
import UGCRes from "@/model/UGCResourceManager";
import Const from "@/const";
import {roundFloat} from "@/utils";

let thumbsFolders = {} // 

export default {
    props:["idx", "pps", "time_sec", "timeline"],
    data(){
        return {
            thumbs:[]
        }
    },
    watch:{
        time_sec: "update",
        "timeline.start_sec": "update",
        "timeline.end_sec": "update"
    },
    computed:{
        firstThumb() {
            let thumb = this.thumbs.find(e => e.url);
            return thumb && thumb.url;
        }
    },
    methods:{
        update() {
            let pts = this.time_sec;
            // window.console.log("thumb time_sec:"+pts);
            if(pts < 0 || pts > this.timeline.offset_sec + this.timeline.end_sec - this.timeline.start_sec)
                return;

            this.fetchThumb(pts);
            this.fetchThumb(pts - DURATION); 
            this.fetchThumb(pts + DURATION); 
        },
        thumbLoadError(index) {
            if(this.thumbs[index].url) {
                this.thumbs.splice(index, 1);
                this.update();
            }
        },
        async prepareCacheFolder(){
            let fileName = ""+this.timeline.id;
            if(!thumbsFolders[fileName]) {

                let fs = await UGCRes.filesystem();

                //getDirectory  exclusive

                let thumbs = await new Promise(
                    fs.root.getDirectory.bind(fs.root,
                        "thumbs/",{create: true}));

                thumbsFolders[fileName] = await new Promise(
                    thumbs.getDirectory.bind(thumbs,
                        fileName,{create: true}));
            }
        },
        async makeThumb2(){},
        async makeThumb(src, des, ss, duration, span) {

            window.console.log("makeThumb:" + des)

            await this.prepareCacheFolder();

            let timebase = 1000000;
            return await new Promise(
                UGCApp.ffmpeg.thumb.bind(null, 
                    src, des, 
                    ss * timebase, 
                    duration * timebase, 
                    span * timebase)
            );
        },
        async fetchThumb(time) {
            let pts = roundFloat(Math.max(0, time + this.timeline.start_sec));

            if(pts > roundFloat(this.timeline.end_sec)) {
                return;
            }

            let thumb = this.thumbs.find(i =>
                roundFloat(i.ss) <= roundFloat(pts) && roundFloat(i.ss + i.duration) >= roundFloat(pts));

            // window.console.log("fetch thumb:" + pts)

            if (!thumb || thumb.preload) {

                let ss = pts - (pts % DURATION);
                let duration = DURATION;
                let span = SPAN;
                let timeline = this.timeline;
                
                let src = timeline.url;
                let des = "{path}/thumbs/{id}/thumb_{ss}_{rn}.jpg"
                    .replace("{path}",(cordova.file.tempDirectory || cordova.file.cacheDirectory))
                    .replace("{id}",timeline.id)
                    .replace("{ss}",ss)
                    // 
                    .replace("{rn}",Math.random().toString(16).replace("0.", ""))

                //

                let loaded = thumb;
                if(loaded) {
                    loaded.loading = true;
                    delete loaded.preload;
                } else {
                    loaded = {
                        loading: true,
                        url: "",
                        ss,
                        duration
                    };
                    this.thumbs.push(loaded);
                }
                this.timeline.thumbs = this.thumbs;

                let ret = await this.makeThumb(src, des, ss, duration, span);
                if (ret) 
                    window.console.error("make thumb error:" + ret);


                delete loaded.loading;
                loaded.url = await clipModel.Util.getCdvUrl(des);
                window.console.debug("make thumb:" + loaded.url);

                if(time == 0) {
                    Editlist.editlist.updateOperateHistory(Const.Operate.VideoAdd.Type, timeline);
                }
            } 
        },
        makeAllThumb() {
            // 
            // this.thumbs
            let pts = this.timeline.start_sec;
            let end = this.timeline.end_sec;
            while(pts <= end) {
                let ss = pts - (pts % DURATION);

                let preload = {
                    preload: true,
                    url: '',
                    ss,
                    duration: DURATION
                };

                this.thumbs.push(preload);
                this.timeline.thumbs = this.thumbs;

                pts += DURATION;
            }
        }
    },
    async mounted(){
        // this.thumbs = thie.timeline.thumbs;
        this.makeAllThumb();
        await this.fetchThumb(0);
    }
}
</script>