<template>
    <div class="image-main">
        <ScrollTab :value.sync="active" color="transparent" indicator-color="red" text-color="white" left>
            <mu-tab v-for="(item,idx) in allSticker" :key="idx">{{item.name}}</mu-tab>
        </ScrollTab>
        <div class="tab-box" v-show="active == idx" v-for="(folder,idx) in allSticker" :key="idx">
            <div v-for="(item) in folder.list" :key="item.name" @click="chooseSticker(item)" class="stickerItem">
                <img loop="0" :src="item.thumb_remote_url+'/'+item.source+'.thumb.apng'">
                <mu-icon v-if="!item.local_file&&!item.download" class="downloadmu-icon" value="arrow_downward" />
                <mu-circular-progress class="loadingmu-icon" v-if="item.download" :value="item.download.percent" :size="60" style="zoom:0.2;margin-left:10px" color="white" :stroke-width="10" />
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>
.image-main{
    display:flex;
    flex-flow: column
}
.tab-box{
    width:100%;padding:5px 10px;display:flex;justify-content:left
}
.stickerItem {
    width: 80px;
    height: 50px;
    position: relative;
    color: white;

    .downloadmu-icon {
        position: absolute;
        right: 0;
        bottom: 0;
        border-radius:10px;
        background:red;
        font-size:10px;
    }

    .loadingmu-icon {
        position: absolute;
        right: 0;
        bottom: 0
    }

    img {
        width: 100%;
        height: 100%;
        object-fit: contain
    }
}
</style>
<script>
import RManager from "@/model/UGCResourceManager"
import ScrollTab from "@/components/ScrollTab"
import Const from "@/const";

export default {
    props:["editlist" ,"postermode"],
    components: {
        ScrollTab
    },
    data(){
        return {
            active:0,
            allSticker: []
        }
    },
    methods:{
        async chooseSticker(item) {
            // window.console.log(item)
            if (!item.local_file && !item.download) {
                await RManager.downloadRes(
                    RManager.ResType.image, item, (percent) => {
                        item.download.percent = Math.ceil(percent * 100);
                    });
            } 
            
            if(item.local_file){

                this.$emit("beforechange",{
                    overlay
                });

                let overlay = this.editlist.add_overlay({
                    end_sec: +item.duration,
                    url: item.local_file.toURL(),
                    type: Const.Overlay.Type.Image
                }, this.postermode);

                this.$emit("change",{
                    overlay
                });

                this.editlist.createOperateHistory(Const.Operate.ImageAdd.Type, overlay.id);
            }
        

        }
    },
    async mounted(){
        this.allSticker = await RManager.fetchResList(RManager.ResType.image);
        window.console.log("allsticker:",this.allSticker);
        this.$emit("update:title", "")
    }
}
</script>