<template>
<div class="overlayToolTabBox">
    <div class="overlayToolTab">
        <template v-if="activeItem && activeItem.text">
            <ClipBtn @click.native="select(TabID.Edit)" class="item-btn" icon="edit" text="" />
            <ClipBtn @click.native="select(TabID.Template)" class="item-btn" icon="format_paint" text="" />
            <ClipBtn @click.native="select(TabID.Settings)" class="item-btn" icon="tune" text="" />
        </template>
        <template>
            <ClipBtn @click.native="select(TabID.Remove)" class="item-btn" icon="delete_forever" text="" />
            <ClipBtn @click.native="select(TabID.Done)" class="done-btn" icon="done"/>
        </template>
    </div>
</div>
</template>
<style lang="less" scoped>
.fade-enter-active {
  transition: all .5s;
}
.fade-leave-active {
  transition: all .2s;
}
.fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade-enter /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.overlayToolTabBox {
    height: 50px;
    position: absolute;
    display: flex;
    justify-content: center;
    width: 100%;
    background: #323232;
    z-index: 100;
}
.overlayToolTab {
    margin-top: 5px;
    border-radius: 5px;
    overflow: hidden;
    background-color: #444;
    display: flex;
    flex-direction: row;
    white-space: nowrap;
    justify-content: center;
    width: auto!important;

    &.hide {
        display: none
    }

    .item-btn {
        padding: 5px 15px;
    }

    .done-btn {
        padding: 0 8px;
        background: #555;
    }
}

</style>
<script>
import ClipBtn from "@/components/ClipBtn";

let __tabid = 0;
const TabID = {
    Edit:__tabid++,
    Template:__tabid++,
    Settings:__tabid++,
    Remove:__tabid++,
    Done:__tabid++
};

export default {
    TabID,
    components: {
        ClipBtn
    },
    props:["activeItem"],
    data() {
        return {
            TabID,
        };
    },
    methods: {
        select(id) {
            this.$emit('select', id, this.activeItem);
        }
    }
};
</script>