<template>
    <div style="width:100%;padding:5px 10px;height:100%;display:flex;flex-direction:column">
        <div style="color:white;padding:5px 0">
            : <span @click="remove" style="background:white;color:gray;border-radius:3px;padding:3px 5px">{{bgm&&bgm.name||""}} {{bgm&&bgm.name&&""}}</span>
        </div>
        <div style="padding:5px 0" class="audio-base-main" @touchend="touchend">
            <span style="color:white;width:100px;white-space:nowrap;padding-right:10px"> ({{volume}}) </span>
            <mu-slider style="margin:0" :min="0" :max="300" :step="1" v-model="volume" />
        </div>
    
        <div style="border-top:1px solid gray;width:100%;display:flex">
            <HScrollBar class="tabs" >
                <div class="tab" @click="pickAudio"></div>
                <div class="tab" @click="active=idx" :class="{active:active==idx}" v-for="(item,idx) in allBGM.filter(i=>i.list)" :key="idx">{{item.name}}</div>
            </HScrollBar>
        </div>

        <div style="height:100%;overflow:auto" :key="list.name" v-show="active == idx" v-for="(list,idx) in allBGM.filter(i=>i.list)">
            <div v-for="item in list.list" :key="list.name+item.name" @click="toggleBGM(item)" 
                :class="{active:selectedBGMName == item.name}" class="bgmItem">
                <mu-icon class="musicmu-icon" size="14" value="music_note" />
                <span class="musicName">{{ item.name }} </span>
                <span class="musicDuration">{{item.duration}}</span>
                <mu-icon v-if="!item.local_file&&!item.download" class="musicPlaymu-icon" value="arrow_downward" />
                <mu-circular-progress v-if="item.download" :value="item.download.percent" :size="60" style="zoom:0.2;margin-left:10px" color="gray" :stroke-width="10" />
            </div>
        </div>
    </div>
</template>
<style lang="less" scoped>

.tabs{
    width:100%;white-space: nowrap;
    padding:5px;
    overflow:auto;
}
.tab{
    display:inline-block;
    color:white;
    padding:10px 10px;
}
.tab.active{
    border-bottom:2px solid red
}
.audio-base-main{
    display:flex;
}

.bgmItem.active {
    background: white;
    border-radius: 5px;
    color: black;
}

.bgmItem {
    color: white;
    display: flex;
    align-items: center;
    font-size: 14px;
    margin:10px 0;

    .musicmu-icon {
        margin: 0 10px;
    }

    .musicPlaymu-icon {
        margin: 0 10px;

        border-radius:10px;
        background:red;
        font-size:10px;
    }

    .musicName {
        max-width: 50%;
        display: inline-block;
    }

    .musicDuration {
        font-size: 12px;
        color:gray;
        margin-left:5px;
        zoom:0.7
    }
}

</style>
<script>
import HScrollBar from "@/components/HScrollBar";
import RManager from "@/model/UGCResourceManager";
import Editlist from "@/model/Editlist";
import Const from "@/const";

export default {
    components:{ HScrollBar },
    data(){
        return {
            active: 0,
            volume: 100,
            allBGM: [],
            bgm:null
        }
    },
    props:["accept"],
    computed:{
        selectedBGMName(){
            return this.bgm?this.bgm.name : ""
        }
    },
    watch:{
        async accept(v) {

            if(!this.bgm) {
                this.$parent.close();
                return;
            }

            await this.stop();
            await new Promise(o => { // 
                let download = () => {
                    if(!this.bgm.local_file) {
                        setTimeout(download, 200);
                    } else {
                        o();
                    }
                };
                download();
            });

            if (v) {
                let oldBgmUrl = Editlist.editlist.bgm.url;

                if(this.bgm){
                    Editlist.editlist.bgm.url = this.bgm.local_file.toNativeURL();
                    Editlist.editlist.bgm.volume = this.volume;
                } else {
                    Editlist.editlist.bgm.url = "";
                }

                if(oldBgmUrl != Editlist.editlist.bgm.url || this.volume != this.initState.volume) {
                    this.$emit("change");

                    if(!Editlist.editlist.bgm.url) {
                        Editlist.editlist.createOperateHistory(Const.Operate.BGMUnset.Type);
                    } else if(!oldBgmUrl){
                        Editlist.editlist.createOperateHistory(Const.Operate.BGMSet.Type);
                    } else if(this.initState.volume != this.volume){
                        Editlist.editlist.createOperateHistory(Const.Operate.BGMParamsModify.Type);
                    }
                }

                this.$parent.close();
            }
        },
        volume(v) {
            if(this._audio) {
                this._audio.volume = Math.min(1, v / 100);
            }
        }
    },
    methods:{
        async toggleBGM(bgm) {
            
            await this.play(bgm);

        },
        remove(){
            this.bgm = null;
            this.stop();
        },
        touchend(){
            // this.$emit('change')
        },
        async stop(){
            //
            if(this._audio){
                this._audio.pause();
                this._audio.removeAttribute("src");
                this._audio.load();
                this._audio.onended && this._audio.onended();
                await new Promise(o=>setTimeout(o,100));
            }
        },
        async play(music) {

            this.bgm = music;

            if (!music.local_file && !music.download) {
                await RManager.downloadRes(RManager.ResType.music, music);
            }

            if(music.local_file){

                await this.stop();

                // 
                if(music.nobgm){
                    this.bgm = null;
                    return;
                }
                
                music.playing = true;
                // this.subList = this.subList.slice(0);//
                await new Promise((o,x)=>{
                    this._audio = new Audio();
                    this._audio.src = music.local_file.toInternalURL();
                    this._audio.onended = o;
                    this._audio.onerror = x;
                    this._audio.volume = Math.min(1, this.volume / 100);
                    this._audio.play();
                });
                music.playing = false;
                // this.subList = this.subList.slice(0);
            }
        },
        async pickAudio() {
            let result = await new Promise(UGCApp.pickAudio.bind(UGCApp));
            if(!result.path) {
                this.$mResToast.error("");
                return;
            }
            let music = await this.getLocalAudio( "file://" + result.path);
            this.play(music);
        },
        async getLocalAudio(url) {
            let fileEntry = await new Promise(resolveLocalFileSystemURL.bind(window, url));
            let music = {
				"name": fileEntry.name,
				"local_file": fileEntry
            }
            return music;
        }
    },
    async mounted(){
        this.$emit("update:title", "")

        this.$parent.acceptable();
        // this.allBGM = [{
        //     name:"",
        //     nobgm:1,
        //     local_file:"local_file",
        // },...(await RManager.fetchResList(RManager.ResType.music))[0].list];
        
        this.allBGM = await RManager.fetchResList(RManager.ResType.music);
        
        this.bgm = this.allBGM.flatMap(i=>i.list).filter(i=>{
            return i.local_file && (
                i.local_file.toNativeURL() == Editlist.editlist.bgm.url);
        })[0];

        if(!this.bgm && Editlist.editlist.bgm.url) {
            this.bgm = await this.getLocalAudio(Editlist.editlist.bgm.url);
        }

        this.volume = Editlist.editlist.bgm.volume;

        this.initState = {
            volume: this.volume
        };
    },
    beforeDestroy(){
        this.stop();
    }
}
</script>