<template>
    <div class="toolTabBox">
        <div class="middleBox">

            <transition name="fade">
                <OverlayToolTab 
                    ref="overlayToolTab" 
                    v-show="activeItem&&activeItem.active"
                    :class="{hide:toolTab!='overlaytool'}" 
                    :activeItem="activeItem"
                    @select="overlayTabSelect"/>
            </transition>

            <transition name="fade">
                <HScrollBar class="m-cut tabBox" :class="{hide:toolTab!='cut'}"  >
                    <ClipBtn @click="select(TabID.VideoAdd)" class="item-btn" icon="note_add" text="" />
                    <ClipBtn @click="select(TabID.VideoRemove)" :disabled="!isCurTimelineRemovable" class="item-btn" icon="delete_forever" text="" />
                    <div style="margin:10px;border-right:1px solid gray" ></div>
                    <ClipBtn @click="select(TabID.VideoSplit)" :disabled="curTimelineIsEnding" class="item-btn" icon="view_agenda" text="" />
                    <ClipBtn @click="select(TabID.VideoBase)" class="item-btn" icon="exposure" text="" />
                    <ClipBtn @click="select(TabID.VideoSort)" class="item-btn" icon="view_column" text="" />
                    <ClipBtn @click="select(TabID.VideoRotate)" :disabled="curTimelineIsEnding" class="item-btn" icon="sync" text="" />
                    <ClipBtn @click="select(TabID.VideoVFlip)" :disabled="curTimelineIsEnding" class="item-btn" icon="swap_vert" text="" />
                    <ClipBtn @click="select(TabID.VideoHFlip)" :disabled="curTimelineIsEnding" class="item-btn" icon="swap_horiz" text="" />
                </HScrollBar>
            </transition>
            <transition name="fade">
                <div class="m-image tabBox" :class="{hide:toolTab!='image'}"  >
                    <ClipBtn @click="select(TabID.AddImage)" class="item-btn" icon="image" text="" />
                    <ClipBtn class="item-btn" icon="photo_library" @click="select(TabID.AddLocalImage)" text="" />
                    <ClipBtn class="item-btn" icon="blur_on" @click="select(TabID.Mosic)" text="" />
                </div>
            </transition>
            <transition name="fade">
                <div class="m-text tabBox" :class="{hide:toolTab!='text'}"  >
                    <ClipBtn @click="select(TabID.AddText)" class="item-btn" icon="textsms" text="" />
                </div>
            </transition>
              <transition name="fade">
                <div class="m-filter tabBox" :class="{hide:toolTab!='filter'}" >
                    <ClipBtn @click="select(TabID.Face)" :disabled="curTimelineIsEnding" class="item-btn" icon="face" text="" />
                    <ClipBtn class="item-btn" icon="photo_filter" :disabled="curTimelineIsEnding" @click="select(TabID.Filter)" text="" />
                </div>
            </transition>
            <transition name="fade">
                <div class="m-music tabBox" :class="{hide:toolTab!='music'}"  >
                    <ClipBtn @click="select(TabID.AudioBase)" class="item-btn" icon="volume_up" text="" />
                    <!-- <ClipBtn class="item-btn" icon="md-microphone" text=""/> -->
                    <ClipBtn @click="select(TabID.BGM)" class="item-btn" icon="surround_sound" text="" />
                    <ClipBtn @click="select(TabID.AudioEffect)" class="item-btn" icon="audiotrack" text="" />
                </div>
            </transition>
        </div>
        <div class="bottomBox">
            <mu-icon value="duo" :class="{active:toolTab=='cut'}" size="28" color="white" @click="toolTab='cut';" />
            <mu-icon value="add_photo_alternate" :class="{active:toolTab=='image'}" size="28" color="white" @click="toolTab='image';" />
            <mu-icon value="post_add" :class="{active:toolTab=='text'}" size="28" color="white" @click="toolTab='text';" />
            <!-- <mu-icon value="movie_filter" :class="{active:toolTab=='filter'}" size="28" color="white" @click="toolTab='filter'" /> -->
            <mu-icon value="music_note" :class="{active:toolTab=='music'}" size="28" color="white" @click="toolTab='music';" />
        </div>
    </div>
</template>
<style lang="less" scoped>

.toolTabBox {
    width: 100%;
    background: rgba(50, 50, 50, 1);
    position: relative;

    &>* {
        width: 100%;
    }

    .middleBox {
        padding: 5px;
        margin-top: 5px;
        // background: #111;

        .tabBox.hide {
            display: none
        }

        .tabBox {
            display: flex;
            flex-direction: row;
            white-space: nowrap;
            justify-content: center;
        }

        .item-btn {
            padding: 5px 20px;
        }

        .m-cut {
            overflow-x: auto;
            justify-content: flex-start
        }


        .m-filter .item-btn.active {
            background: white;
            color: black;
            border-radius: 5px;
        }
    }

    .bottomBox {
        display: flex;
        flex-flow: row;
        padding: 10px 0px;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        background: black;
        border-top: 0.5px solid #666;

        &>*.active {
            color: transparent !important;
            background: linear-gradient(45deg, white 50%, red 50%);
            -webkit-background-clip: text;
            background-clip: text;
        }
    }

}
</style>

<script>
import ClipBtn from "@/components/ClipBtn";
import HScrollBar from "@/components/HScrollBar";
import OverlayToolTab from "@/views/Bottom/OverlayToolTab";
import Const from "@/const";

let __tabid = 0;
let TabID = {
    VideoAdd:__tabid++,
    VideoRemove:__tabid++,
    VideoRotate:__tabid++,
    VideoSort:__tabid++,
    VideoSplit:__tabid++,
    VideoVFlip:__tabid++,
    VideoHFlip:__tabid++,
    VideoBase:__tabid++,

    AddImage:__tabid++,
    AddLocalImage:__tabid++,
    AddText:__tabid++,
    AudioBase:__tabid++,
    BGM:__tabid++,
    Face:__tabid++,
    Filter:__tabid++,
    Mosic:__tabid++,
    AudioEffect:__tabid++,
    AspectRatio:__tabid++,

    ImageTab:__tabid++,
    TextTab:__tabid++,
    AudioTab:__tabid++,
    OtherTab:__tabid++,
    
    ToolEdit:__tabid++,
    ToolTemplate:__tabid++,
    ToolSettings:__tabid++,
    ToolRemove:__tabid++,
    ToolDone:__tabid++
}

export default {
    TabID,
    components:{
        ClipBtn,OverlayToolTab,HScrollBar
    },
    props:["activeItem","editlist"],
    data(){
        return {
            TabID,
            toolTab:'cut'
        }
    },
    watch:{
        activeItem(o) {
            if(o) {
                if(o.type == Const.Overlay.Type.Text) {
                    this.toolTab = 'text';
                } else if(o.type == Const.Overlay.Type.Image ||
                        o.type == Const.Overlay.Type.Mosic) {
                    this.toolTab = 'image';
                }
            }
        },
        
        toolTab(v){
            if(v == 'image')
                this.$emit("select",TabID.ImageTab);
            else if(v == 'text')
                this.$emit("select",TabID.TextTab);
            else if(v == 'music')
                this.$emit("select",TabID.AudioTab);
            else
                this.$emit("select",TabID.OtherTab);
        }
    },
    computed:{
        isCurTimelineRemovable() {
            let timelines = this.editlist.timelines;
            if(timelines.length <= 1) {
                return false;
            }
            let hasEnding = timelines[timelines.length - 1].ending;
            if(hasEnding) {
                let cur = this.editlist.cur_timeline;
                if(cur.ending) {
                    return true;
                }
                if(timelines.length <= 2) {
                    return false;
                }
            }
            return true;
        },
        curTimelineIsEnding() {
            let cur = this.editlist.cur_timeline;
            return cur && cur.ending;
        }
    },
    methods:{
        select(tabID){
            this.$emit("select",tabID);
        },
        overlayTabSelect(toolTabID, item){
            let tabID;
            switch(toolTabID){
                case OverlayToolTab.TabID.Edit: tabID = TabID.ToolEdit;break;
                case OverlayToolTab.TabID.Template: tabID = TabID.ToolTemplate;break;
                case OverlayToolTab.TabID.Settings: tabID = TabID.ToolSettings;break;
                case OverlayToolTab.TabID.Remove: tabID = TabID.ToolRemove;break;
                case OverlayToolTab.TabID.Done: tabID = TabID.ToolDone;break;
                default: return;
            }
            this.$emit("select", tabID, item);
        },
    }
}
</script>