<template>
    <div class="controlBox">
        <mu-icon value="aspect_ratio" @click="$emit('event',{eventID:EventID.AspectRatio})" style="position:absolute;left:0" />
        <mu-icon value="skip_previous" @click="$emit('event',{eventID:EventID.SeekBegin})" />
        <mu-icon v-show="!playing" value="play_arrow" @click="$emit('event',{eventID:EventID.Play})" />
        <mu-icon v-show="playing" value="pause" @click="$emit('event',{eventID:EventID.Pause})" />
        <mu-icon value="skip_next" @click="$emit('event',{eventID:EventID.SeekEnd})" />
        <mu-icon value="undo" :class="[hasHistory ? '' : 'disabled']" @click="hasHistory && $emit('event',{eventID:EventID.Undo})" style="position:absolute;right:0" />
    </div>
</template>
<style lang="less" scoped>
.controlBox {
    text-align: center;
    color: white;
    font-size: 25px;

    &>* {
        padding: 10px 20px;
    }
}
.disabled {
    opacity: 0.5;
}
</style>
<script>
import Editlist from "@/model/Editlist"; 

let EventID = {
    Play:0,
    Pause:1,
    SeekBegin:2,
    SeekEnd:3,
    AspectRatio:4,
    Undo:5
}
export default {
    EventID,
    props:["playing"],
    data(){
        return {
            EventID,
            editlist: Editlist.editlist
        }
    },
    computed: {
        hasHistory() {
            return this.editlist.operate.history.length > 1;
        }
    },
    created() {
        // Editlist.editlist.operate.history
        Editlist.editlist.operate = this.editlist.operate;
    }
}
</script>