<template>
    <div class="native-video-main" :class="{show:showscreen}">
    </div>
</template>
<style scoped>
.native-video-main{
    background:black!important
}
.native-video-main.show{
    background:transparent!important
}
</style>
<script>
/***
 * src, play, pause, seek
 * 
 * position, duration
 * 
 * meta, timeupdate
 */
import cordovaModel from "@/model/CordovaModel"
import ResizeObserver from 'resize-observer-polyfill'

let EventID = {
    Meta: 1,
    Render: 2,
    Timeupdate: 3,
    Finished: 4,
    Error: 5,
    End: 6,
    SeekDone: 7,
    toString(id){
        for(let i in EventID){
            if(EventID[i] == id)
                return i
        }
    },
};

let FFPlay = {
    rect(){},
    play(){},
    running(){},
    pause(){},
    stop(){},
    seek(){},
    duration(){},
    position(){},
    mute(){},
    thumb(){},
    refresh(){},
    setEventCallback(){}
}

export default {
    data(){
        return {
            cmd: "",
            ready: false,
            playing: false,
            loaded: false,
            keep_screen: false,
            lastPosition: 0,
            timeupdateTmr: null
        }
    },
    computed:{
        showscreen(){
            return true||this.loaded || this.keep_screen;
        }
    },
    watch:{
        playing(p){
            this.$emit("playing", p);
        },
    },
    methods:{
        async resize(){
            let rect = this.$el.getBoundingClientRect();
            await this.rect(rect);
            this.$emit('resize');
        },
        async rect(rect) {
            if(!rect.width || !rect.height)return;

            window.console.log("ffplay rect: "+[rect.x, rect.y, rect.width, rect.height].join(","))
            await new Promise(FFPlay.rect.bind(null,rect.x, rect.y, rect.width, rect.height));
            
            // android: 
            await new Promise(o=>setTimeout(o,150));
            await new Promise(FFPlay.refresh.bind(null));
        },
        async load(cmd, keep_screen) {
            await this.stop();
            this.ready = false;
            this.playing = false;
            this.loaded = false;
            this.keep_screen = cmd && keep_screen;
            this.cmd = cmd;
            FFPlay.setEventCallback(this.onEvent);
            if(cmd){
                await new Promise(
                    FFPlay.play.bind(null,cmd));

                await new Promise((o,x)=>{
                    let timeout = 5*1000;
                    let i = setInterval(()=>{
                        timeout -= 100;
                        if(timeout<0){
                            clearInterval(i);
                            x();
                        }
                        else if(this.loaded){
                            clearInterval(i);
                            o();
                        }
                    },100)
                })
            }
        },
        async running() {
            return await new Promise(
                FFPlay.running
            );
        },
        resume() {
            this.playing = true;
            FFPlay.mute(0);
            FFPlay.resume();
        },
        pause() {
            this.playing = false;
            FFPlay.mute(1);
            FFPlay.pause();
        },
        async stop() {
            this.playing = false;
            return await new Promise(
                FFPlay.stop);
        },
        async seek(pos) {
            if(pos == -1)
                pos = await this.duration();

            this.lastPosition = pos;
            this.playing = false;
            FFPlay.seek(pos);
        },
        async duration() {
            return await new Promise(
                FFPlay.duration);
        },
        async position() {
            return await new Promise(
                FFPlay.position);
        },
        onResume(){
            FFPlay.refresh();
        },
        async onEvent(event) {
            window.console.log("got player event: "+ EventID.toString(event));
            switch(event){
                case EventID.Meta:{

                    // rect 
                    // await new Promise( o=>setTimeout(o, 500));
                    // FFPlay.refresh();
                    this.$emit("meta",{
                        duration: await this.duration()
                    });
                } break;
                case EventID.Render:{
                    if(!this.loaded){
                        await new Promise( o=>setTimeout(o, 100));
                        await this.resize();
                        this.loaded = true;
                    }
                    this.$emit("render");
                } break;
                case EventID.Timeupdate:{
                    // this.$emit("timeupdate",
                    //     await this.position()
                    // );
                } break;
                case EventID.Finished:{
                    this.$emit("finished");
                } break;
                case EventID.End:{
                    this.loaded = false;
                    this.$emit("end");
                } break;
                case EventID.SeekDone:{
                    this.$emit("seekdone");
                } break;

            }
        }
    },
    mounted(){
        if(cordovaModel.isCordova()) {
            FFPlay = UGCApp.ffplay;
            this.resize();

            document.addEventListener("resume", this.onResume, false);
        }

        this._resizeOb = new ResizeObserver(() => {
            this.resize();
        });

        this._resizeOb.observe(this.$el);

    },
    beforeDestroy(){
        document.removeEventListener("resume", this.onResume, false);
        this._resizeOb.disconnect();
    }
}
</script>