<template>
    <div class="face-main" >
        <mu-switch v-model="enabled" color="red" />
        <span style="color:white;width:100px"></span>
        <mu-slider :disabled="!enabled" style="margin:0" :min="0" :max="10" :step="1" v-model="face.level" />
        <div style="width:50px;text-align:center">{{face.level}}</div>
    </div>
</template>
<style scoped>
.face-main{
    display:flex;
    white-space: nowrap;
    color:white;
    line-height: 25px;
}
.face-main > *{
    margin:0 10px;
}
</style>
<script>
import Editlist from "@/model/Editlist";
import ResourceManager from "@/model/UGCResourceManager";
import Const from "@/const";

export default {
    data(){
        return {
            enabled:false,
            face:{level:10}
        }
    },
    watch:{
        "enabled":async function(e){
            if(!e){
                Editlist.editlist.beauty_level = 0;
            }
            else{
                if(this.face.level == 0) {
                    this.face.level = 10;
                }
                if(!Editlist.editlist.face.beauty_source){
                    Editlist.editlist.beauty_source = await ResourceManager.getBeautySource();
                }
                Editlist.editlist.beauty_level = this.face.level;
            }
        },
        "face.level":function(l){
            Editlist.editlist.beauty_level = l;
        }
    },
    methods: {
        onDialogClose() {
            if(this.initeState) {
                if(this.initeState.enable != this.enabled) {
                    if(this.enabled) {
                        Editlist.editlist.createOperateHistory(Const.Operate.FilterEnable.Type);
                    } else {
                        Editlist.editlist.createOperateHistory(Const.Operate.FilterDisable.Type);
                    }
                } else if(this.initeState.level != Editlist.editlist.beauty_level) {
                    Editlist.editlist.createOperateHistory(Const.Operate.FilterModify.Type);
                }
            }
        },
    },
    async mounted(){
        this.$emit("update:title", "")
        this.enabled = Editlist.editlist.face.beauty_level > 0;
        this.face.level = Editlist.editlist.face.beauty_level;
        this.initeState = {
            enabled: this.enabled,
            level: this.face.level
        };
    }
}
</script>