<template>
<div class="filter-box">
    <div :style="{visibility:(filter && filter.percent >= 0?'':'hidden')}" class="filter-slider">
        <span >{{filter&&filter.name}}</span>
        <mu-slider class="slider" :min="0" :max="100" :step="5" color="red" v-model="percent" />
        <span style="width:50px;text-align:center">{{percent}}</span>
    </div>
    <mu-tabs :value.sync="active" color="transparent" indicator-color="red" text-color="white"  left>
        <mu-tab v-for="(item,idx) in allFilter.filter(i=>i.list)" :key="idx">{{item.name}}</mu-tab>
    </mu-tabs>
    <div class="filter-main"  >
        <ClipBtn @click.native="toggleEffect()" 
            :class="{active:noactive}" 
            class="item-btn" 
            icon="check" text="" />
        <div style="margin:10px;height:60%;border-right:1px solid gray"></div>
        <template v-for="(item,idx) in allFilter.filter(i=>i.list)">
            <div style="display:flex" v-if="active == idx" :key="idx">
                <ClipBtn v-for="e in item.list" :key="e.name" @click.native="toggleEffect(e)" 
                    :active="filter && (e.name == filter.name)"
                    class="item-btn" 
                    :src="e.thumb_remote_url" :text="e.name" >
                    <mu-icon v-if="isNeedDownload(e)" class="downloadmu-icon" value="arrow_downward" />
                    <mu-circular-progress class="loadingmu-icon" v-if="e.download" :value="e.download.percent" :size="60" style="zoom:0.2;margin-left:10px" color="white" :stroke-width="10" />
                </ClipBtn>
            </div>
        </template>
    </div>
</div>
</template>
<style scoped>

.filter-box{
    display: flex;
    flex-flow: column;
    width:100%;
}
.filter-slider > .slider{
    margin:0 10px;
}
.filter-slider{
    display: flex;
    color: white;
    padding-bottom:10px;
    border-bottom: 1px solid #222;
    white-space: nowrap;
    font-size:12px;
    line-height:25px;
}
.filter-main{
    display: flex;
    height: 70px;
    overflow:auto;
    width:100%;
}
.item-btn {
    padding: 5px 10px;
    width: 60px;
}

.downloadmu-icon {
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius:10px;
    background:red;
    font-size:10px;
}

.loadingmu-icon {
    position: absolute;
    right: 0;
    bottom: 0
}
</style>
<script>
import extend from "extend";
// import Editlist from "@/model/Editlist";
import ClipBtn from "@/components/ClipBtn";
import RManager from "@/model/UGCResourceManager";
import Const from "@/const";

export default {
    components:{ClipBtn},
    props:["editlist"],
    data(){
        return {
            allFilter: [],
            active: 0,
            percent:100,
            filter:null,
        }
    },
    watch:{
        "percent":function(p){
            this.editlist.filter_percent = p;
            if(this.filter)
                this.filter.percent = p;
        }
    },
    computed:{
        
        noactive(){
            return !this.filter
        }
    },
    methods:{
        isNeedDownload(res){
            return (
                !res.local_file || (
                    res.source_list && res.source_list.filter((i,idx) => 
                        !res.local_file_list[idx]
                    ).length || false
                )
            ) && !res.download;
        },
        async toggleEffect(e) {
            if(!e){
                this.editlist.filter.length = 0;
                this.editlist.filter_reset = "";
                this.filter = null;
                // this.$emit("change", e);
                return;
            }

            if (!this.filter || e.name != this.filter.name){

                if (this.isNeedDownload(e)) {
                    await RManager.downloadRes(
                        RManager.ResType.filter, e, (percent) => {
                            e.download.percent = Math.ceil(percent * 100);
                        });
                } 
                if (e.local_file) {
                    this.editlist.filter.length = 0;
                    this.editlist.filter.push(e);
                    this.editlist.filter_reset = [
                        e.local_file.toNativeURL(), 
                        ...(
                            e.local_file_list && 
                            e.local_file_list.map(i=>i.toNativeURL()) || []
                        )
                    ].map(i=>i.replace("file://","")).join("|");

                    this.filter = e;
                    e.percent = 100;
                }
                // this.$emit("change", e);
            }
        },
        onDialogClose(){
            if(this.initeState) {
                let modifyState = [this.initeState.filter, ...this.editlist.filter].map(e => {
                    return {
                        oldEnable: this.initeState.filter.find(f => f.name == e.name),
                        currEnable: this.editlist.filter.find(f => f.name == e.name)
                    }
                });

                if(this.editlist.filter.length > 2 &&
                        modifyState.find(e => e.oldEnable != e.currEnable)) {
                    this.editlist.createOperateHistory(Const.Operate.FilterModify.Type);
                    
                } else if(this.editlist.filter.length <= 1 &&
                        modifyState[0].currEnable != modifyState[0].oldEnable) {
                    if(modifyState[0].currEnable) {
                        this.editlist.createOperateHistory(Const.Operate.FilterEnable.Type);
                    } else {
                        this.editlist.createOperateHistory(Const.Operate.FilterDisable.Type);
                    }

                } else if(this.percent != this.initeState.percent) {
                    this.editlist.createOperateHistory(Const.Operate.FilterModify.Type);
                }
            }
        }
    },
    async mounted(){
        this.$emit("update:title", "")
        this.allFilter = await RManager.fetchResList(RManager.ResType.filter);
        if(this.editlist.filter.length){
            this.allFilter.forEach(i=>{
                let found = i.list && i.list.filter(i=>{
                    return i.name == this.editlist.filter[0].name
                })[0];
                if(found){
                    this.filter = found;
                }
            })
            
        }
        this.initState = {
            percent: this.editlist.filter_percent,
            filter: extend(true, [], this.editlist.filter)
        };
    }
}
</script>