<template>
<div v-resize="updateUISize" ref="mainbox" @click="boxClick">
    <div tabindex="-1" v-if="overlay" ref="main" class="clip_img" @touchstart="touchstart" @touchmove="touchmove"
             @touchend="hideMagnet" @touchcancel="hideMagnet" :style="style">
        <!-- url=url+'?'+overlay.id,urlonload -->
        <img v-show="loaded" @load="imgLoaded" @error="imgLoadError" :src="url + '?' + overlay.id" :key="url" class="img" ref="img" />
        <div class="border" />
        <div class="remove-btn item-btn" @click="remove">
            <mu-icon value="close" />
        </div>
        <div v-if="edit_filter && edit_filter(overlay)" class="edit-btn item-btn" @click="$emit('edit', overlay)">
            <mu-icon value="edit" />
        </div>
        <div
            class="resize-btn item-btn"
            @touchstart.stop="resize_start"
            @touchmove.stop="resize_move"
            @touchend.stop="resize_end"
        ><mu-icon value="code" /></div>
    </div>
    <div class="magnet-x" v-if="magnetX" :style="{left: magnetX+'px'}"></div>
    <div class="magnet-y" v-if="magnetY" :style="{top: magnetY+'px'}"></div>
</div>
</template>
<style>
.clip_img {
    position: absolute;
    width: 100px;
    height: 100px;
    left: 0;
    top: 0;
    display: flex;
    outline: 0
}
</style>
<style scoped>
.img {
    display: block;
    width: 100%;
    height: 100%;
}
.border {
    position:absolute;
    width:100%;
    height:100%;
    border: 1px solid white;
}
.item-btn {
    width: 30px;
    height: 30px;
    min-width: 30px;
    min-height: 30px;
    background:white;
    display:flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    border-radius: 30px;
    position: absolute;
    opacity: 0.8
}
.resize-btn {
    right: -20px;
    bottom: -20px;
    transform:rotate(45deg)
}
.remove-btn {
    top: -20px;
    left: -20px;
}
.edit-btn {
    top: -20px;
    right: -20px;
}
.magnet-x {
    position: absolute;
    height:100%;
    top: 0;
    border-left: 1px dashed white;
}
.magnet-y {
    position: absolute;
    width:100%;
    left: 0;
    border-top: 1px dashed white;
}
</style>
<script>
import Editlist from "@/model/Editlist";
import ClipModel from "@/model/ClipModel";
import Const from "@/const";
import { rotateVector } from "@/utils";
import Magnet from "@/model/Magnet";

function angleAndScale(pointA, pointB, pointC) {
    var lengthAB = Math.sqrt(
            Math.pow(pointA.X - pointB.X, 2) + Math.pow(pointA.Y - pointB.Y, 2)
        ),
        lengthAC = Math.sqrt(
            Math.pow(pointA.X - pointC.X, 2) + Math.pow(pointA.Y - pointC.Y, 2)
        ),
        lengthBC = Math.sqrt(
            Math.pow(pointB.X - pointC.X, 2) + Math.pow(pointB.Y - pointC.Y, 2)
        );
    var cosA =
        (Math.pow(lengthAB, 2) +
            Math.pow(lengthAC, 2) -
            Math.pow(lengthBC, 2)) /
        (2 * lengthAB * lengthAC);

    //
    var angleA = Math.round((Math.acos(cosA) * 180) / Math.PI);

    // 
    //  p1=(x1,y1) p2=(x2,y2) p3=(x3,y3)
    // 
    // p12=(x2-x1,y2-y1)
    // p23=(x3-x2,y3-y2)
    //  p12  p23 
    // p12 x p23 = (x2-x1)*(y3-y2)-(y2-y1)*(x3-x2)
    // p1-p2-p3 
    // p1-p2-p3 
    // p1-p2-p3 

    let sign =
        (pointB.X - pointA.X) * (pointC.Y - pointB.Y) -
        (pointB.Y - pointA.Y) * (pointC.X - pointB.X);

    if (sign < 0) angleA = 180 + 180 - angleA;

    //
    return {
        angle: angleA,
        scale: lengthAC / lengthAB
    };
}
function getElementPageXY(element) {
    var actualLeft = element.offsetLeft;
    var parent = element.offsetParent;
    while (parent != null) {
        actualLeft +=
            parent.offsetLeft + (parent.offsetWidth - parent.clientWidth) / 2;
        parent = parent.offsetParent;
    }

    var actualTop = element.offsetTop;
    parent = element.offsetParent;
    while (parent != null) {
        actualTop +=
            parent.offsetTop + (parent.offsetHeight - parent.clientHeight) / 2;
        parent = parent.offsetParent;
    }
    return { x: actualLeft, y: actualTop };
}

export default {
    props: ["time_sec", "overlays_in", "mosics_in", "postermode", "edit_filter"],
    data() {
        return {
            userModify: false,
            overlay: null,
            loaded: false,
            ignoreLoadEvent: false,
            url: "",
            magnetX : null,
            magnetY : null,
            box: {
                width: 0,
                height: 0,
                offsetX: 0, //sar
                offsetY: 0,
                scale: 1 // ui  
            },
            style: {
                left: "50px",
                top: "50px",
                width: "100px",
                height: "100px",
                transform: "rotate(0deg)"
            },
            temp: {
                pageX: 0,
                pageY: 0,
                offsetX: 0,
                offsetY: 0
            },
            tempStyle: {
                x: 0,
                y: 0,
                w: 0,
                h: 0,
                rotate: 0
            }
        };
    },
    watch: {
        // 
        "overlay.url": "load"
    },
    computed: {
        allOverlays() {
            return [...this.overlays_in, ...this.mosics_in];
        },
        hasActive(){
            return !!this.overlay;
        },
        layouts() {
            let layouts = this.allOverlays().map(overlay => {
                //ffmpeg  overlay x,y 
                let box = Math.hypot(overlay.w, overlay.h);
                let x = overlay.x;
                let y = overlay.y;
                if(overlay.type != Const.Overlay.Type.Mosic) {
                    x -= (overlay.w - box)/2;
                    y -= (overlay.h - box)/2;
                }
                let layout = { id: overlay.id, type: overlay.type };
                layout.left = (this.box.offsetX + x / this.box.scale);
                layout.top = (this.box.offsetY + y / this.box.scale) ;
                layout.width = overlay.w / this.box.scale;
                layout.height = overlay.h / this.box.scale;
                layout.rotate = overlay.rotate;
                layouts.push(layout);
            });
            window.console.log("layouts style");
            return layouts;
        }
    },
    methods: {
        updateUISize() {
            this.box.width = this.$refs.mainbox.clientWidth;
            this.box.height = this.$refs.mainbox.clientHeight;

            let darUI = this.box.width / this.box.height;
            let dar = Editlist.editlist.dar[0] / Editlist.editlist.dar[1];

            if(dar > darUI){
                this.box.offsetY = (this.box.height - this.box.width / dar)/2;
                this.box.offsetX = 0;
                this.box.scale = Editlist.editlist.resolution_dar[0] / this.box.width;
            } else {

                this.box.offsetX = (this.box.width - this.box.height * dar)/2;
                this.box.offsetY = 0;
                this.box.scale = Editlist.editlist.resolution_dar[1] / this.box.height ;
            }

            window.console.log("ClipImg update box: " + JSON.stringify(this.box));
        },
        boxClick(e) {

            if(this.hasActive){
                this.blur();
                return;
            }
            
            let { x, y } = getElementPageXY(this.$refs.mainbox);
            x = ((e.pageX - x) - this.box.offsetX) * this.box.scale;
            y = ((e.pageY - y) - this.box.offsetY) * this.box.scale;

            let selectedFilter = this.allOverlays.filter(o=>{
                //time_sec  overlay 
                return o.offset_sec <= this.time_sec && (
                    o.end_sec == 0 || 
                    o.offset_sec + o.end_sec >= this.time_sec
                );
            }).filter(o=>{
                //x,y inset overlay rect

                let box = Math.hypot(o.w,o.h);
                let ox = o.x;
                let oy = o.y;

                if(o.type != Const.Overlay.Type.Mosic) {
                    ox -= (o.w - box) / 2;
                    oy -= (o.h - box) / 2;
                }

                // overlay
                // x, y
                let p = rotateVector(ox + o.w / 2, oy + o.h /2, x, y, -o.rotate);

                return ox < p.x && oy < p.y && ox + o.w > p.x && oy + o.h > p.y;
            });

            if(selectedFilter.length){
            
                let item = selectedFilter.reverse()[0];
                this.active(item);
                this.load();
                this.$emit("overlayActive", item);
            } else {
                this.blur();
            }
        },
        async blur() {
            if(this.overlay){
                this.overlay.enabled = true;
                this.overlay.active = false;
                if(this.overlay.new){
                    this.overlay.new = false;
                    await this.$emit("change");
                }
                else {
                    if(this.overlay.type == Const.Overlay.Type.Mosic) {
                        Editlist.editlist.update_mosic(this.overlay);
                    } else {
                        await Editlist.editlist.update_overlay(this.overlay, this.postermode);
                    }
                }

                if(this.userModify) {
                    this.createOrUpdateOperateHistory();
                }
                let overlay = this.overlay;
                this.overlay = null;
                // this.$emit("change");
                this.$emit("overlayDeactive", overlay);
            }
        },
        async active(overlay) {

            // palyer  overlay
            // if(overlay.enabled){
            //     overlay.enabled = false;
            //     this.$emit("change");
            // }

            if(this.overlay) {
                this.overlay.active = false;
            }

            overlay.active = true;
            this.overlay = overlay;
            this.userModify = false;

            if(this.overlay.type == Const.Overlay.Type.Mosic) {
                this.loaded = false;
                this.url = "";
                Editlist.editlist.update_mosic(this.overlay);
            } else {
                Editlist.editlist.update_overlay(this.overlay, this.postermode);
            }

            this.updateUI();

            if(!overlay.url && overlay.w <= 10) {// 
                this.toCenter(1);
                this.onChange(true);
            }

            this.$nextTick(()=>{
                this.$refs.main.focus();
            })

            window.console.log("ClipImg active: " + JSON.stringify(this.overlay));
        },
        updateUI() {
            this.updateUISize();

            if(this.overlay) {
                window.console.log("layout style");
                //ffmpeg  overlay x,y 
                let box = Math.hypot(this.overlay.w, this.overlay.h);
                let x = this.overlay.x;
                let y = this.overlay.y;
                if(this.overlay.type != Const.Overlay.Type.Mosic) {
                    x -= (this.overlay.w - box)/2;
                    y -= (this.overlay.h - box)/2;
                }

                this.style.left = (this.box.offsetX + x / this.box.scale) + "px";
                this.style.top = (this.box.offsetY + y / this.box.scale) + "px";
                this.style.width = this.overlay.w / this.box.scale + "px";
                this.style.height = this.overlay.h / this.box.scale + "px";
                this.style.transform = "rotate(" + this.overlay.rotate + "deg)";
            }
        },
        imgLoaded() {
            this.loaded = true;
            if(this.ignoreLoadEvent || !this.overlay ||
                !this.$refs.img.naturalWidth || !this.$refs.img.naturalHeight) {
                return;
            }
            this.setDefaultSize();
        },
        setDefaultSize() {
            let modify = false;
            let oldWidth = this.style.width;

            let ratio =
                this.$refs.img.naturalWidth / this.$refs.img.naturalHeight;
            if(+this.style.width.replace("px","") < 10){
                this.toCenter(ratio);
                modify = true;
            }
            this.style.width =
                ratio * this.style.height.replace("px", "") + "px";
            
            if(Math.ceil(this.style.width+0) != Math.ceil(oldWidth+0)) {
                modify = true;
            }

            if(modify) {
                this.onChange(true);
            }
        },
        imgLoadError(e) {
            window.console.log("img load error ", e);
        },
        toCenter(ratio) {
            let width = this.box.width / 2 - this.box.offsetX;
            let height = this.box.height /2 - this.box.offsetY;
            if(ratio > 1)
                height = width / ratio;
            else 
                width = height * ratio;

            this.style.left = (this.box.width - width) / 2 + "px";
            this.style.top = (this.box.height - height) / 2 + "px";
            this.style.width = width + "px";
            this.style.height = height + "px";
        },
        remove() {

            this.overlay.active = false;

            let idx = this.overlays_in.findIndex(e => e.id == this.overlay.id);
            if(idx >= 0)
                this.overlays_in.splice(idx, 1);

            if(this.mosics_in) {
                idx = this.mosics_in.findIndex(e => e.id == this.overlay.id);
                if(idx >= 0)
                    this.mosics_in.splice(idx, 1);
            }

            this.overlay = null;
            this.$emit("change");
        },
        resize_start(e) {
            this.temp.pageX = e.changedTouches[0].pageX;
            this.temp.pageY = e.changedTouches[0].pageY;

            this.tempStyle.x = +this.style.left.replace("px", "");
            this.tempStyle.y = +this.style.top.replace("px", "");

            this.tempStyle.w = +this.style.width.replace("px", "");
            this.tempStyle.h = +this.style.height.replace("px", "");

            //
            let { x, y } = getElementPageXY(this.$refs.main);
            this.temp.offsetX =
                this.temp.pageX - (x - this.tempStyle.x);
            this.temp.offsetY =
                this.temp.pageY - (y - this.tempStyle.y);

            this.tempStyle.rotate = +this.style.transform.match(
                /\d+/
            )[0];

            return true;
        },
        resize_move_scale(e) {
            let dX = e.changedTouches[0].pageX - this.temp.pageX;
            let dY = e.changedTouches[0].pageY - this.temp.pageY;
            

            // angle = angle + this.tempStyle.rotate;
            // this.style.transform = "rotate(" + angle + "deg)";

            let dW = dX;//(scale - 1) * this.tempStyle.w;
            let dH = dY;//(scale - 1) * this.tempStyle.h;

            this.style.left = this.tempStyle.x - dW / 2 + "px";
            this.style.top = this.tempStyle.y - dH / 2 + "px";
            this.style.width = this.tempStyle.w + dW + "px";
            this.style.height = this.tempStyle.h + dH + "px";

            this.onChange();
            e.preventDefault && e.preventDefault();
            e.returnValue = false;
            e.stopPropagation && e.stopPropagation();
            return false;
        },
        resize_move(e) {
            if(this.overlay.type == Const.Overlay.Type.Mosic){
                return this.resize_move_scale(e);
            }
            let dX = e.changedTouches[0].pageX - this.temp.pageX;
            let dY = e.changedTouches[0].pageY - this.temp.pageY;

            //
            let { angle, scale } = angleAndScale(
                {
                    X: this.tempStyle.x + this.tempStyle.w / 2,
                    Y: this.tempStyle.y + this.tempStyle.h / 2
                },
                {
                    X: this.temp.offsetX,
                    Y: this.temp.offsetY
                },
                {
                    X: this.temp.offsetX + dX,
                    Y: this.temp.offsetY + dY
                }
            );

            angle = angle + this.tempStyle.rotate;
            this.style.transform = "rotate(" + angle + "deg)";

            let dW = (scale - 1) * this.tempStyle.w;
            let dH = (scale - 1) * this.tempStyle.h;

            this.style.left = this.tempStyle.x - dW / 2 + "px";
            this.style.top = this.tempStyle.y - dH / 2 + "px";
            this.style.width = this.tempStyle.w + dW + "px";
            this.style.height = this.tempStyle.h + dH + "px";

            this.onChange();
            e.preventDefault && e.preventDefault();
            e.returnValue = false;
            e.stopPropagation && e.stopPropagation();
            return false;
        },
        resize_end() {},
        touchstart(e) {
            this.temp.pageX = e.changedTouches[0].pageX;
            this.temp.pageY = e.changedTouches[0].pageY;
            this.tempStyle.x = +this.style.left.replace("px", "");
            this.tempStyle.y = +this.style.top.replace("px", "");
        },
        touchmove(e) {
            let newX =
                this.tempStyle.x +
                e.changedTouches[0].pageX -
                this.temp.pageX;
            let newY =
                this.tempStyle.y +
                e.changedTouches[0].pageY -
                this.temp.pageY;

            // dock begin
            let oldLayout = this.createMagnetLayout({
                left: this.tempStyle.x,
                top: this.tempStyle.y,
                width: + this.style.width.replace('px', ''),
                height: + this.style.height.replace('px', '')
            });
            let newLayout = this.createMagnetLayout({
                left: newX,
                top: newY,
                width: oldLayout.width,
                height: oldLayout.height
            });
            newLayout = this.magnet.check(oldLayout, newLayout);
            newX = newLayout.left;
            newY = newLayout.top;
            // dock end

            this.style.left = newX + "px";
            this.style.top = newY + "px";
            this.onChange();

            e.preventDefault && e.preventDefault();
            e.returnValue = false;
            e.stopPropagation && e.stopPropagation();
            return false;
        },
        onChange(auto) {

            this.overlay.x = (+this.style.left.replace("px", "") - this.box.offsetX) * this.box.scale ;
            this.overlay.y = (+this.style.top.replace("px", "") - this.box.offsetY) * this.box.scale;
            this.overlay.w = +this.style.width.replace("px", "") * this.box.scale;
            this.overlay.h = +this.style.height.replace("px", "") * this.box.scale;
            this.overlay.rotate = +this.style.transform.match(/\d+/)[0];

            if(this.overlay.type != Const.Overlay.Type.Mosic) {
                //ffmpeg  overlay x,y 
                let box = Math.hypot(this.overlay.w, this.overlay.h);
                this.overlay.x += (this.overlay.w - box)/2;
                this.overlay.y += (this.overlay.h - box)/2;
            }

            this.overlay.x = +this.overlay.x.toFixed(2);
            this.overlay.y = +this.overlay.y.toFixed(2);
            this.overlay.w = +this.overlay.w.toFixed(2);
            this.overlay.h = +this.overlay.h.toFixed(2);

            // setTimeout(() => {
                if(this.overlay.type == Const.Overlay.Type.Mosic) {
                    Editlist.editlist.update_mosic(this.overlay);
                } else {
                    Editlist.editlist.update_overlay(this.overlay, this.postermode);
                }
            // }, 500);

            window.console.log("overlay changed: "+
                this.overlay.x +","+
                this.overlay.y +","+
                this.overlay.w +","+
                this.overlay.h);


            if(auto) {
                this.createOrUpdateOperateHistory(true);
            } else {
                this.userModify = true;
            }
        },
        async load() {
            if(this.overlay && this.overlay.url) {
                // loadedonload
                this.ignoreLoadEvent = true;
                this.loaded = false;
                
                let url;

                try {
                    url = await ClipModel.Util.getCdvUrl(this.overlay.url);
                } catch (error) {
                    url = this.overlay.url; // PC
                }
                this.ignoreLoadEvent = false;

                if(!this.overlay) {
                    return;
                }

                // if(this.overlay.type == Const.Overlay.Type.Text) {
                //     // url
                //     this.url = url + "?" + Math.random();
                // } else {
                //     // apng,random
                //     this.url = url;
                // }
                // url
                this.url = url;
            }
        },
        createOrUpdateOperateHistory(isAuto){
            if(isAuto) {
                if(this.overlay.type == Const.Overlay.Type.Mosic) {
                    Editlist.editlist.updateOperateHistory(Const.Operate.MosicAdd.Type, this.overlay);
                } else if(this.overlay.type == Const.Overlay.Type.Image) {
                    Editlist.editlist.updateOperateHistory(Const.Operate.ImageAdd.Type, this.overlay);
                } else {
                    Editlist.editlist.updateOperateHistory(Const.Operate.TextAdd.Type, this.overlay);
                }
            } else if(this.overlay.type == Const.Overlay.Type.Mosic) {
                Editlist.editlist.createOperateHistory(Const.Operate.MosicModify.Type);
            } else if(this.overlay.type == Const.Overlay.Type.Image) {
                Editlist.editlist.createOperateHistory(Const.Operate.ImageModify.Type);
            } else {
                Editlist.editlist.createOperateHistory(Const.Operate.TextModify.Type);
            }
        },
        createMagnetLayout(layout) {
            return {
                ...layout,
                right: layout.left + layout.width,
                bottom: layout.top + layout.height
            }
        },
        hideMagnet() {
            this.magnetX = null;
            this.magnetY = null;
        }
    },
    mounted(){
        // this.onChange();

        let mainbox = this.$refs.mainbox;
        this.magnet = new Magnet({
            span: 10, // px
            features: ['middle', 'center'],
            render: (x, y) => {
                this.magnetX = x;
                this.magnetY = y;
            },
            dynamicLayouts: () => {
                return [this.createMagnetLayout({
                    left: 0,
                    top: 0,
                    width: mainbox.offsetWidth,
                    height: mainbox.offsetHeight
                })];
            }
        });
    }
};
</script>