<template>
    <span class="icon" :class="{active:active,disabled:disabled}" @click="clicked">
        <mu-icon v-if="!src" :value="icon" />
        <div v-else-if="src" class="img">
            <img :src="src"/>
            <div />
        </div>
        <span >{{text}}</span>
        <slot />
    </span>
</template>
<style scoped>
.disabled {
    opacity: 0.5;
}
.icon{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size:24px;
    color:white;
    padding:5px;
    position: relative;
}
.icon > span{
    zoom:0.5;
    white-space: nowrap;
}
.icon > .img{
    border-radius:3px;
    width:30px;height:30px;
    overflow:hidden;
    position: relative;
}
.icon > .img > img{
    width:100%;height:100%;object-fit: cover
}
.icon > .img > div {
    display:none;
}
.icon.active > .img > div{
    position:absolute;top:0;left:0;height:100%;width:100%;
    background:rgba(255,0,0,0.6);
    display:block
}

.icon.active{
    color:red;
}
</style>
<script>
export default {
    props: ["text", "icon", "src", "ricon", "active", "disabled"],
    methods: {
        clicked(e) {
            if(!this.disabled) {
                this.$emit('click', e);
            }
        }
    }
}
</script>