<template>
    <NativeVideo 
        ref="video" 
        @resize="$emit('resize')"
        @meta="meta($event);$emit('render')"
        @playing="$emit('playing',$event)"
        @finished="$emit('finished')"
        @render="render($event);$emit('render')"
        @seekdone="seekdone($event);$emit('seekdone')"
        @timeupdate="$emit('timeupdate',$event)" />

</template>
<style scoped>

</style>
<script>
import Editlist from "@/model/Editlist";
import NativeVideo from "@/components/NativeVideo";
import UGCRes from "@/model/UGCResourceManager";
// import throttle from "@/assets/js/throttle";
import throttle from "lodash.throttle";

export default {
    components:{NativeVideo},
    props:[
        "editlist",
        "pause",
        // "time_sec",
        "postermode"
    ],
    data(){
        return {
            seeking:false,
            throttle_ing:false
        }
    },
    watch:{
        pause(p){
            if(p)
                this.$refs.video.pause();
            else
                this.$refs.video.resume();
        },
        // time_sec(sec){
        //     this.$refs.video.seek(sec);
        // }
    },
    methods:{
        meta(){},
        doSeek:throttle(function(sec){
            window.console.log(`event seek to ${sec}`);
            this.$refs.video.seek(sec);
            this.throttle_ing = false;
        },1000),
        seek(sec){
            this.seeking = true;
            this.throttle_ing = true;
            this.doSeek(sec);
            Editlist.editlist.time_sec = sec;
        },
        seekdone(){
            // window.console.log(this.doSeek);
            window.console.log(`event throttle_ing: ${this.throttle_ing}`);
            if(!this.throttle_ing)
                this.seeking = false;
        },
        async render(){
            if(!Editlist.editlist.poster.url){

                await new Promise(UGCApp.ffplay.refresh);
                // window.console.log("refresh: "+ret)
                
                let dir = await UGCRes.getTempFolder();
                
                let des = dir.nativeURL + "/poster.jpg";

                await new Promise(UGCApp.ffplay.thumb.bind(null, des.replace("file://","")));

                Editlist.editlist.poster.url = des + "?" + Math.random();
            }
        },
        
        async reload(keep_screen){
            Editlist.editlist = this.editlist;
            if(!Editlist.editlist.transform_source)
                try{
                    window.console.log("get transfromsource")
                    Editlist.editlist.transform_source = await UGCRes.getTransformSource();
                    window.console.log`get transfromsource done:${Editlist.editlist.transform_source}`
                    window.transform_source = Editlist.editlist.transform_source;
                } catch(e){
                    //get error
                    window.console.log(e)
                }

            if(this.postermode)
                await this.$refs.video.load(Editlist.getPosterPlayerParam(), keep_screen);
            else
                await this.$refs.video.load(Editlist.getPlayerParam(), keep_screen);
        }
    }
}
</script>