<template>
    <div class="sort_main">

        <Container @drop="onDrop" :auto-scroll-enabled="false" orientation="horizontal" :drag-begin-delay="200" class="sort_box">            
          <template v-for="t in timelines">
            <Draggable class="sort_item" v-if="!t.ending" :key="t.id">
                <div class="sort_head">{{(t.end_sec - t.start_sec).toFixed(1)}}</div>
                <div class="sort_thumb">
                    <img v-if="t.thumbs.length && t.thumbs[0].url" :src="t.thumbs[0].url"/>
                </div>
            </Draggable>
          </template>
        </Container>
        
        <div class="sort_tip">
            
        </div>
    </div>
</template>
<style>
.smooth-dnd-disable-touch-action * {
    touch-action: inherit!important;
}
/*  */
.sort_main .smooth-dnd-container.horizontal {
    display: flex;
}
</style>
<style scoped>
.sort_main{
    width:100%;
}
.sort_box{
    width:100%;
    overflow:auto;
    white-space: nowrap;
    display:flex;
}
.sort_box::-webkit-scrollbar-track-piece {
  background-color: transparent;
}
.sort_box::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.sort_box::-webkit-scrollbar-thumb {
  border: 1px solid #666;
  background-image:linear-gradient(to left, #666 0%,  #666 50%, #777 50%);
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.5) inset;
  background-size: 2px 100%;
  background-position: center;
  background-clip: padding-box;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  min-height: 28px;
}
.sort_box::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.5);
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
}
.sort_item{
    display:inline-block!important;
    padding: 10px;
}

.sort_head{
    color:white;
    text-align: center;
    width:40px;
}
.sort_thumb{
    height:40px;
    width:40px;
    overflow:hidden;
}
.sort_thumb img{
    height:100%;
    width:100%;
    object-fit: cover;
    object-position: left;
}
.sort_thumb {
    background-color: #000;
}
.sort_tip{
    color:white;
}
</style>
<script>

// vue-smooth-dnd
//  https://github.com/kutlugsahin/vue-smooth-dnd/

import extend from "extend";
import { Container, Draggable } from "vue-smooth-dnd";
import Editlist from "@/model/Editlist";
import Const from "@/const";

export default {
    props: ["accept"],
    components:{
        Container,
        Draggable
    },
    data(){
        return {
            timelines:[]
        }
    },
    watch:{
        accept(a){
            if(a){
                Editlist.editlist.update_timelines(this.timelines);
                Editlist.editlist.timelines = this.timelines;
                this.$emit("change", "sort");

                if(this.initState.timelines.find((e,i) => this.timelines[i].id != e.id)) {
                    Editlist.editlist.createOperateHistory(Const.Operate.VideoSort.Type);
                }
            }
            this.$parent.close();
        }
    },
    methods:{
        onDrop (dropResult) {
            const { removedIndex, addedIndex, payload } = dropResult;
            if (removedIndex === null && addedIndex === null) return;

            const result = [...this.timelines];
            let itemToAdd = payload

            if (removedIndex !== null) {
                itemToAdd = result.splice(removedIndex, 1)[0]
            }

            if (addedIndex !== null) {
                result.splice(addedIndex, 0, itemToAdd)
            }

            this.timelines = result;

        }
    },
    mounted(){
        this.timelines = Editlist.editlist.timelines;
        this.$emit("update:title", "")
        this.$parent.acceptable();
        this.initState = {
            timelines: extend(true, [], this.timelines)
        }
    },
}
</script>