<template>
    <div class="main">

        <div
            class="main-maker"
            v-show="!makePoster&&!makeEnding&&!doExport"
        >
            <ClipTop @export="doExport = true" />
            <div
                ref="mainBox"
                class="main-box"
            >
                <PreviewPlayer
                    ref="preview"
                    :pause="player.pause || doExport"
                    :editlist="editlist"
                    @finished="playerFinished"
                    @resize="!makePoster&&!makeEnding&&!doExport"
                    class="main-video"
                />

                <ClipImg
                    ref="overlayEditor"
                    @change="clipImgChange"
                    @overlayActive="overlayActive"
                    @overlayDeactive="overlayDeactive"
                    :overlays_in="editlist.overlay"
                    :mosics_in="editlist.mosic"
                    :time_sec="editlist.time_sec"
                    class="overlay-box"
                />
            </div>

            <div
                ref="toolBox"
                class="toolBox"
            >
                <PlayerController
                    v-show="showPlayerController"
                    :playing="!player.pause"
                    @event="playerControllerEvent"
                />

                <ClipTimeline
                    ref="timeline"
                    v-show="showTimeline"
                    :timelines_in="editlist.timelines"
                    :show_overlay="showOverlay"
                    :overlay_type="overlayType"
                    :show_amix="showAMix"
                    :playing="!player.pause"
                    :poster="editlist.poster.url"
                    @timelinesChange="timelinesChange"
                    @timeChange="timeChange"
                    @overlayChange="overlayChange"
                    @overlayActive="overlayActive"
                    @amixChange="amixChange"
                    @amixActive="amixActive"
                    @on_poster_click="makePoster=1"
                    @on_ending_click="makeEnding=1"
                    style=""
                />
                <ToolTab
                    ref="toolTab"
                    v-show="showTooltab"
                    :editlist="editlist"
                    :activeItem="activeItem"
                    @select="tabSelect"
                />
                <ToolDialog
                    :editlist="editlist"
                    @close="showTooltab=showPlayerController=showTimeline=true;mainBoxResized()"
                    @beforechange="toolBeforeChange"
                    @change="toolValueChange"
                    ref="toolDlg"
                />

            </div>

            <div
                v-if="importProgress >= 0"
                class="spin"
            >
                <div>
                    <mu-circular-progress mode="determinate" :value="importProgress*100">
                    </mu-circular-progress>
                    <br>
                    <span style="font-size:14px"> {{ (importProgress*100).toFixed(2) }}%</span>
                </div>
            </div>
        </div>
        <PosterMaker
            v-if="makePoster"
            :editlist="this.editlist"
            @close="makePoster = false; seekPlayer(0); reloadPlayer(); "
        />
        <EndingMaker
            v-if="makeEnding"
            @close="makeEnding = false; reloadPlayer();"
        />
        <Exporter
            ref="exporter"
            v-if="doExport"
            @close="doExport = false"
        />
        <Importer
            ref="importer"
            :editlist="editlist"
            @select="importFileSelect"
            @progress="importProgress=$event"
        />
    </div>

</template>

<style lang="less" scoped>
