    background: rgba(0, 0, 0, 0.8);
    color: white;
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &>*{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align:center;
        flex-direction: column;
    }
