</style>

<script>
import extend from "extend";
import ClipTop from "@/views/Top/ClipTop";
import ClipTimeline from "@/views/Bottom/ClipTimeline";
import PlayerController from "@/views/Bottom/PlayerController";
import ToolTab from "@/views/Bottom/ToolTab";
import ToolDialog from "@/views/Bottom/ToolDialog";
import ClipImg from "@/views/Editor/ClipImg";
import clipModel from "@/model/ClipModel.js";
import PreviewPlayer from "@/views/PreviewPlayer";
import PosterMaker from "@/views/PosterMaker";
import EndingMaker from "@/views/EndingMaker";
import Exporter from "@/views/Exporter";
import Importer from "@/views/Helper/Importer";
import Editlist from "@/model/Editlist";
import UGCResourceManager from "@/model/UGCResourceManager";
// import OverlayToolTab from "@/views/Bottom/OverlayToolTab";
import Const from "@/const";
import { roundFloat, rotateVector } from "@/utils";

window.clipModel = clipModel;

let console = window.console;

export default {
    components: {
        ClipTop,
        ClipTimeline,
        PlayerController,
        ToolTab,
        ToolDialog,
        ClipImg,
        PreviewPlayer,
        PosterMaker,
        EndingMaker,
        Exporter,
        Importer
    },
    data() {
        return {
            player: {
                pause: true,
                // time_sec: 0
            },
            editlist: Editlist.editlist,
            showOverlay: false, //  / 
            overlayType: false, // /  
            showAMix: false, //   
            showTooltab: true, // 
            showPlayerController: true,
            showTimeline: true,
            activeItem: null, // //
            makePoster: false, // 
            makeEnding: false, // 
            doExport: false, // 
            importProgress: -1
        };
    },
    methods: {
        async reloadPlayer(dont_keep_screen) {
            await this.$refs.preview.reload(!dont_keep_screen);
        },
        seekPlayer(sec) {
            // window.console.log("seek:" + sec);
            this.$refs.preview.seek(sec);
            this.$refs.timeline.seek(sec);
        },
        playerFinished() {
            console.log("finished");
            this.player.pause = true;
        },
        timeChange(e) {
            // console.log("seek:" + e.time_sec);
            this.player.pause = true;
            this.seekPlayer(e.time_sec);
        },
        timelinesChange(timelines) {
            this.editlist.timelines = timelines;
            this.editlist.createOperateHistory(Const.Operate.VideoTimeModify.Type);
            // this.reloadPlayer();
        },
        amixChange(o) {
            this.editlist.update_amix(o);
            // this.reloadPlayer();
            this.editlist.createOperateHistory(Const.Operate.AudioEffectTimeModify.Type);
        },
        amixActive(o) {
            // this.$refs.overlayToolTab.active(o);
            this.activeItem = o;
        },
        overlayChange(o) {
            this.$refs.overlayEditor.active(o);
            // this.$refs.overlayToolTab.active(o);

            this.editlist.update_overlay(o);
            // this.reloadPlayer();

            if(o.type == Const.Overlay.Type.Image) {
                this.editlist.createOperateHistory(Const.Operate.ImageTimeModify.Type);
            } else if(o.type == Const.Overlay.Type.Text) {
                this.editlist.createOperateHistory(Const.Operate.TextTimeModify.Type);
            } else if(o.type == Const.Overlay.Type.Mosic) {
                this.editlist.createOperateHistory(Const.Operate.MosicTimeModify.Type);
            }
        },
        overlayActive(o) {
            // this.$refs.overlayToolTab.active(o);
            if (o) {
                this.$refs.overlayEditor.active(o);
            } else {
                this.$refs.overlayEditor.blur();
                if(this.activeItem && this.activeItem.active) {
                    this.activeItem.active = false;
                }
            }
            this.activeItem = o;
        },
        overlayDeactive() {
            this.activeItem = null;
        },
        blankClicked() {
            this.$refs.overlayEditor.blur();
        },
        playerControllerEvent(e) {
            let cur_timeline;
            let time_sec;

            switch (e.eventID) {
                case PlayerController.EventID.Play:
                    if(!this.$refs.preview.seeking&&this.$refs.preview.$refs.video.loaded)
                        this.player.pause = false;
                    break;
                case PlayerController.EventID.Pause:
                    this.player.pause = true;
                    break;
                case PlayerController.EventID.SeekBegin:
                    this.player.pause = true;
                    cur_timeline = this.editlist.cur_timeline;
                    if (!cur_timeline && this.editlist.timelines.length) {
                        // 
                        cur_timeline = this.editlist.timelines[
                            this.editlist.timelines.length - 1
                        ];
                    }
                    time_sec = roundFloat(this.editlist.time_sec);
                    if (cur_timeline) {
                        if (time_sec == roundFloat(cur_timeline.offset_sec)) {
                            // 
                            let prevIdx = Math.max(
                                0,
                                this.editlist.timelines.findIndex(
                                    e => e.id == cur_timeline.id
                                ) - 1
                            );
                            cur_timeline = this.editlist.timelines[prevIdx];
                        }
                        (async ()=>{
                            await new Promise(o=>setTimeout(o,100));
                            this.seekPlayer(cur_timeline.offset_sec);
                        })();
                    }
                    break;
                case PlayerController.EventID.SeekEnd:
                    this.player.pause = true;
                    cur_timeline = this.editlist.cur_timeline;
                    time_sec = roundFloat(this.editlist.time_sec);
                    if (cur_timeline) {
                        if (
                            time_sec ==
                            roundFloat(
                                cur_timeline.offset_sec +
                                    cur_timeline.end_sec -
                                    cur_timeline.start_sec
                            )
                        ) {
                            // 
                            let nextIdx =
                                this.editlist.timelines.indexOf(
                                    cur_timeline
                                ) + 1;
                            if (nextIdx < this.editlist.timelines.length) {
                                cur_timeline =
                                    this.editlist.timelines[nextIdx];
                            }
                            // 0.001
                            // this.player.time_sec = cur_timeline.offset_sec + cur_timeline.start_sec + 0.001;
                        }

                        // 
                        (async ()=>{
                            await new Promise(o=>setTimeout(o,100));
                            this.seekPlayer(
                                cur_timeline.offset_sec +
                                    cur_timeline.end_sec -
                                    cur_timeline.start_sec
                            );
                        })()
                    }
                    break;
                case PlayerController.EventID.Undo:
                    this.undo();
                    break;
                case PlayerController.EventID.AspectRatio:
                    this.$refs.toolDlg.show(ToolTab.TabID.AspectRatio, {
                        height: this.$refs.toolTab.clientHeight + "px"
                    });
                    break;
            }
        },
        toolBeforeChange() {
            // 
            this.player.pause = true;
        },
        toolValueChange(data, tabid) {
            // 
            if (tabid == ToolTab.TabID.AspectRatio) {
                this.player.pause = true;
            }

            if (data) {
                if (data.overlay) {
                    this.overlayActive(data.overlay);
                    
                    if(data.overlay.type == Const.Overlay.Type.Text) {
                        this.reloadPlayer();
                    }
                    // this.$refs.overlayEditor.active(data.overlay);
                    // this.$refs.overlayToolTab.active(data.overlay);
                    return;
                }
            }
            this.reloadPlayer();
        },
        clipImgChange() {
            // if(!this.$refs.overlayEditor.overlay){
            //     this.activeItem = null;
            // }
            this.reloadPlayer();
        },
        // overlayTabSelect(tabID, overlay) {
        //     let dialogTabId = null;
        //     let edit = false;

        //     switch(tabID) {
        //         case OverlayToolTab.TabID.Edit:
        //         case OverlayToolTab.TabID.Template:
        //         case OverlayToolTab.TabID.Settings:
        //             if(overlay.type == Const.Overlay.Type.Text) {
        //                 dialogTabId = ToolTab.TabID.AddText;
        //                 edit = true;
        //             }
        //             break;
        //         case OverlayToolTab.TabID.Remove:
        //             // TODO remove
        //             break;
        //         case OverlayToolTab.TabID.Done:
        //             this.$refs.overlayEditor.blur();
        //             break;
        //         default:
        //             break;
        //     }

        //     if(edit) {
        //         this.showTooltab = false;
        //         this.$refs.toolDlg.show(dialogTabId, {
        //             overlay,
        //             overlayToolTabId: tabID
        //         });
        //     }
        // },
        tabSelect(tabID) {
            let ToolTaba = ToolTab.TabID;
            console.log(ToolTaba);
            switch (tabID) {
                case ToolTab.TabID.ImageTab:
                    this.showOverlay = true;
                    this.overlayType = Const.Overlay.Type.Image | Const.Overlay.Type.Mosic;
                    this.showAMix = false;
                    break;
                case ToolTab.TabID.TextTab:
                    this.showOverlay = true;
                    this.overlayType = Const.Overlay.Type.Text;
                    this.showAMix = false;
                    break;
                case ToolTab.TabID.AudioTab:
                    this.showAMix = true;
                    this.showOverlay = false;
                    break;
                case ToolTab.TabID.OtherTab:
                    // //  
                    // this.showOverlay = true;
                    // // this.overlayType = Const.Overlay.Type.Mosic;
                    // this.showAMix = false;

                    // 
                    this.showOverlay = false;
                    this.showAMix = false;
                    break;

                case ToolTab.TabID.ToolEdit:
                case ToolTab.TabID.ToolTemplate:
                case ToolTab.TabID.ToolSettings:
                    if (this.activeItem.type == Const.Overlay.Type.Text) {
                        this.showTimeline = false;
                        this.showPlayerController = false;

                        this.$refs.toolDlg.show(ToolTab.TabID.AddText, {
                            height: "auto",
                            overlay: this.activeItem,
                            operateToolTabId: tabID
                        });
                    }
                    break;
                case ToolTab.TabID.ToolDone:
                    this.activeItem.active = false;
                    this.activeItem = null;
                    this.$refs.overlayEditor.blur();
                    break;
                case ToolTab.TabID.ToolRemove:
                    this.editlist.remove_overlay(this.activeItem.id);
                    this.activeItem.active = false;

                    this.$refs.overlayEditor.blur();
                    this.activeItem = null;
                    this.reloadPlayer();
                    break;

                case ToolTab.TabID.VideoAdd:
                    this.pickVideo();
                    break;
                case ToolTab.TabID.VideoRemove:
                    {
                        let timelines = this.editlist.timelines;

                        // 
                        let idx = timelines.findIndex(o => o.active);
                        if (idx == -1) {
                            // 
                            let curr_timeline = this.editlist.cur_timeline;
                            if (!curr_timeline && timelines.length) {
                                // 
                                idx = 0;
                            } else {
                                idx = timelines.findIndex(
                                    e => e.id == curr_timeline.id
                                );
                            }
                        }
                        if (idx >= 0) {
                            let timeline = timelines[idx];
                            timelines.splice(idx, 1);
                            [0, ...timelines].reduce((pre, nxt) => {
                                nxt.offset_sec = pre;
                                return (
                                    nxt.offset_sec + nxt.end_sec - nxt.start_sec
                                );
                            });

                            // 
                            [
                                ...this.editlist.amix,
                                ...this.editlist.overlay
                            ].forEach(e => {
                                let found =
                                    roundFloat(timeline.offset_sec) <=
                                        roundFloat(e.offset_sec) &&
                                    roundFloat(e.offset_sec) <
                                        roundFloat(
                                            timeline.offset_sec +
                                                timeline.end_sec -
                                                timeline.start_sec
                                        );
                                if (found) {
                                    this.editlist.remove_overlay(e.id);
                                }
                            });

                            this.reloadPlayer();

                            // 
                            if (idx == 0 || !timelines.length) {
                                this.seekPlayer(0);
                            } else {
                                this.seekPlayer(
                                    timelines[idx - 1].offset_sec +
                                        timelines[idx - 1].end_sec -
                                        timelines[idx - 1].start_sec
                                );
                            }
                            
                            this.editlist.createOperateHistory(Const.Operate.VideoRemove.Type, timeline.id);
                        }

                        if (timelines.length == 0) {
                            this.editlist.poster.url = "";
                            this.editlist.poster.overlay.splice(0, -1);
                            this.pickVideo();
                        }
                    }
                    break;
                case ToolTab.TabID.VideoSplit:
                    {
                        this.player.pause = true;

                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        let time_sec = roundFloat(this.$refs.timeline.time_sec);
                        timelines.forEach((o, i) => {
                            if (
                                roundFloat(o.offset_sec) < time_sec &&
                                roundFloat(
                                    o.end_sec - o.start_sec + o.offset_sec
                                ) > time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];
                            let second = extend(true, {}, found, {
                                id: this.editlist.timelineId
                            });

                            found.active = false;

                            if (
                                time_sec - found.offset_sec <
                                    this.editlist.video_min_sec ||
                                found.offset_sec +
                                    found.end_sec -
                                    found.start_sec -
                                    time_sec <
                                    this.editlist.video_min_sec
                            ) {
                                this.$mResToast.error(
                                    "1"
                                );
                                return;
                            }

                            found.end_sec = second.start_sec =
                                time_sec - found.offset_sec + found.start_sec;

                            timelines.splice(idx + 1, 0, second);
                            timelines.reduce((pre, nxt, idx) => {
                                if (idx == 1) pre = pre.end_sec - pre.start_sec;
                                nxt.offset_sec = pre;
                                return (
                                    nxt.offset_sec + nxt.end_sec - nxt.start_sec
                                );
                            });

                            this.$refs.timeline.refreshUI();
                            this.reloadPlayer();

                            this.editlist.createOperateHistory(Const.Operate.VideoSplit.Type);
                        } else {
                            this.$mResToast.error("1");
                        }
                    }
                    break;
                case ToolTab.TabID.VideoRotate:
                    {
                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        timelines.forEach((o, i) => {
                            if (
                                o.offset_sec <= this.editlist.time_sec &&
                                o.end_sec - o.start_sec + o.offset_sec >
                                    this.editlist.time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];

                            if(idx == 0) {
                                // 
                                this.editlist.poster.rotate += 270;
                                this.editlist.poster.rotate %= 360;
                                this.rotatePosterOverlay(found, 270);
                            }

                            found.rotate += 270;
                            found.rotate %= 360;

                            this.reloadPlayer();

                            this.editlist.createOperateHistory(Const.Operate.VideoRotate.Type);
                        }
                    }
                    break;
                case ToolTab.TabID.VideoVFlip:
                    {
                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        timelines.forEach((o, i) => {
                            if (
                                o.offset_sec < this.editlist.time_sec &&
                                o.end_sec - o.start_sec + o.offset_sec >
                                    this.editlist.time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];
                            found.vflip = !found.vflip;
                            this.reloadPlayer();

                            this.editlist.createOperateHistory(Const.Operate.VideoVFlip.Type);
                        }
                    }
                    break;
                case ToolTab.TabID.VideoHFlip:
                    {
                        let timelines = this.editlist.timelines;
                        let idx = -1;
                        timelines.forEach((o, i) => {
                            if (
                                o.offset_sec < this.editlist.time_sec &&
                                o.end_sec - o.start_sec + o.offset_sec >
                                    this.editlist.time_sec
                            )
                                idx = i;
                        });
                        if (idx >= 0) {
                            let found = timelines[idx];
                            found.hflip = !found.hflip;
                            this.reloadPlayer();

                            this.editlist.createOperateHistory(Const.Operate.VideoHFlip.Type);
                        }
                    }
                    break;
                case ToolTab.TabID.AddLocalImage:
                    this.pickImg();
                    break;
                case ToolTab.TabID.Mosic:
                    this.addMosic();
                    break;
                default: {
                    let height;

                    switch (tabID) {
                        case ToolTab.TabID.Face:
                        case ToolTab.TabID.AudioBase:
                            height = this.$refs.toolTab.clientHeight + "px";
                            break;
                        case ToolTab.TabID.AddText:
                            this.showTimeline = false;
                            this.showPlayerController = false;
                            height = "auto";
                            break;
                        case ToolTab.TabID.BGM:
                            height = this.$el.clientHeight / 2 + "px";
                            break;
                        default:
                            height = this.$refs.toolBox.clientHeight + "px";
                    }

                    this.$refs.toolDlg.show(tabID, {
                        height
                    });
                }
            }

            if(tabID == ToolTab.TabID.ImageTab ||
                tabID == ToolTab.TabID.TextTab ||
                tabID == ToolTab.TabID.AudioTab||
                tabID == ToolTab.TabID.OtherTab) {
                this.overlayActive(null);
            }
        },
        reset() {},
        async pickImg() {
            // native cordova.camera plugin  content:// 
            // CameraLauncher.java::processResultFromGallery::707  fileLocation
            let file = await new Promise((o, x) =>
                navigator.camera.getPicture(o, x, {
                    sourceType: Camera.PictureSourceType.PHOTOLIBRARY,
                    allowEdit: false,
                    correctOrientation: true,
                    mediaType: Camera.MediaType.PICTURE
                })
            );

            // window.console.log(file);
            if (!file) return;

            let overlay = this.editlist.add_overlay({
                url: file,
                type: Const.Overlay.Type.Image
            });
            this.$refs.overlayEditor.active(overlay);
            this.overlayActive(overlay);
            // this.$refs.overlayToolTab.active(overlay);

            this.editlist.createOperateHistory(Const.Operate.ImageAdd.Type);
        },
        async importFileSelect(files, auto, isRetry) {
            // try {
                let time_sec = this.editlist.total_sec;
                let timelines = await Promise.all(
                    files.map(async val => {
                        let src = val.uri;
                        // (await new Promise(
                        //     resolveLocalFileSystemURL.bind(null,val.uri))
                        // ).toNativeURL();

                        let format = await new Promise(
                            UGCApp.ffmpeg.format.bind(null, src)
                        );

                        if(!format.duration) {
                            // 
                            isRetry && this.$mResToast.error("");
                            return;
                        }

                        if ((90 + format.video.rotate) % 180 == 0) {
                            let _cacheWidth = format.video.width;
                            format.video.width = format.video.height;
                            format.video.height = _cacheWidth;
                        }

                        // offset_sec += format.duration/1000;
                        return {
                            url: src,
                            src_rotate: format.video.rotate,
                            width: format.video.width,
                            height: format.video.height,
                            start_sec: 0,
                            end_sec: format.duration / 1000,
                            total_sec: format.duration / 1000
                        };
                    })
                );

                timelines = timelines.filter( e => e);

                //
                if(timelines.length == 0 && !isRetry) {
                    setTimeout(() => 
                        this.importFileSelect(files, auto, true)
                    , 500);
                    return;
                }

                timelines = timelines.filter(e => e);
                if(!timelines.length) {
                    return;
                }

                this.editlist.add_timelines(timelines);

                if(!auto) {// reload
                    timelines.forEach(t => {
                        this.editlist.createOperateHistory(Const.Operate.VideoAdd.Type, t.id);
                    });
                }

                await this.$refs.preview.reload();

                this.seekPlayer(time_sec);

                this.importProgress = -1;
            // } catch (error) {
            //     console.log("import file:", error);
            // }

            //  
            if(!this.editlist.timelines.length) {
                this.pickVideo();
            }
        },
        async pickVideo() {
            this.$refs.importer.open();
        },
        mainBoxResized() {
            this.$nextTick(() => {
                this.$refs.overlayEditor.updateUI();
            });
        },
        async addMosic() {
            if(!this.editlist.mosic_source) {
                this.editlist.mosic_source = await UGCResourceManager.getMosicSource();
            }
            let mosic = this.editlist.add_mosic();
            
            await this.reloadPlayer();

            this.activeItem = mosic;
            this.$refs.overlayEditor.active(mosic);
            this.$refs.overlayEditor.toCenter(1);

            this.editlist.createOperateHistory(Const.Operate.MosicAdd.Type, mosic.id);
        },
        async undo() {
            let his = this.editlist.undoOperateHistory();
            if(his) {
                let undoOp = his.undo; // 
                let restoreOp = his.restore; // 

                let restoreArray = (target, src) => {
                    target.splice(0, target.length);
                    src.forEach(e => target.push(extend(true, {}, e)));
                };

                restoreArray(this.editlist.timelines, restoreOp.timelines);
                restoreArray(this.editlist.amix, restoreOp.amix);
                restoreArray(this.editlist.overlay, restoreOp.overlay);
                restoreArray(this.editlist.mosic, restoreOp.mosic);
                restoreArray(this.editlist.filter, restoreOp.filter);

                this.editlist.face = restoreOp.face;
                this.editlist.video = restoreOp.video;
                this.editlist.audio = restoreOp.audio;
                this.editlist.bgm = restoreOp.bgm;
                this.editlist.poster = restoreOp.poster;

                let undoOpKey = Object.keys(Const.Operate).find(k=>Const.Operate[k].Type == undoOp.operateType);
                
                let target;
                switch(undoOp.operateType) {
                    case Const.Operate.VideoRemove.Type:
                        target = this.editlist.timelines.find(e => undoOp.targetId == e.id);
                        target.active = true;
                        this.seekPlayer(target.offset_sec);
                        break;
                }

                if(this.editlist.time_sec > this.editlist.total_sec) {
                    this.seekPlayer(this.editlist.total_sec);
                }

                this.player.pause = true;
                this.$refs.overlayEditor.blur();
                if(this.activeItem) {
                    this.activeItem.active = false;
                }

                this.reloadPlayer();
                
                this.$mResToast.success(" " + Const.Operate[undoOpKey].Name);
                window.console.log("undo: ", restoreOp);

                // 
                if(!this.editlist.timelines.length) {
                    this.pickVideo();
                }
            } else {
                this.$mResToast.success("");
            }
        },
        // overlay
        rotatePosterOverlay(timeline, rotate) {
            // overlay
            let width = this.editlist.getRotatedWidth(timeline);
            // 
            let ox = width;
            let oy = 0;

            this.editlist.poster.overlay.forEach(overlay => {

                let box = Math.hypot(overlay.w, overlay.h);
                let x = overlay.x - (overlay.w - box)/2;
                let y = overlay.y - (overlay.h - box)/2;

                let rotatedLT = rotateVector(ox, oy, x, y, rotate);
                let rotatedRB = rotateVector(ox, oy, x + overlay.w, y + overlay.h, rotate);
                // 
                ox = (rotatedLT.x + rotatedRB.x) / 2;
                oy = (rotatedLT.y + rotatedRB.y) / 2;
                rotatedLT = rotateVector(ox, oy, rotatedLT.x, rotatedLT.y, -rotate);
                // 
                rotatedLT.x -= width;

                overlay.x = rotatedLT.x + (overlay.w - box)/2;
                overlay.y = rotatedLT.y + (overlay.h - box)/2;
                overlay.rotate += rotate;
                overlay.rotate %= 360;
            });
        }
    },
    async mounted() {
        if (!window.UGCApp) {
            // this.$Message.error("UGC")
            // this.$router.back();
            console.log("no ugcapp");
            return;
        }

        // let rect = this.$refs.mainVideoArea.getClientRects()[0];
        // this.$refs.mainVideoArea.rect(rect);

        // document.addEventListener("backbutton", this.onBack, false);

        let config = await this.editlist.getConfig();

        // if(this.isIOS()){
        //     config = {input:null}
        // }

        if(config.input) {
            // SDK
            await this.importFileSelect([{
                uri: config.input
            }], true);
        } else {
            this.pickVideo(i => {
                console.log(i);
                if (i.status == "progress") {
                    let progress = i.index;
                    this.$data.importProgress = progress;
                } else {
                    this.$data.importProgress = -1;
                }
            });
        }

        this.editlist.createOperateHistory(Const.Operate.Init.Type);
    },
    async beforeDestroy() {
        // document.removeEventListener("backbutton", this.onBack, false);
    }
};
</script>
