    display: flex;
    color: white;
    align-items: center;
    justify-content: space-between;
    padding-right: 20px;
    height:60px;
}

.btn-done {
    float: right;
}

.content {
    text-align: center;
    margin: 20px 0;
    position: relative;
    align-items: center;
    justify-content: center;
    display:flex;
    flex:1;
    min-height: 100px;/*flex */

    img {
        object-fit: contain;
        width:100%;height:100%
    }

    video {
        height: 100%;
        width: 100%;
    }

