</style>

<script>
import Editlist from "@/model/Editlist";
import ClipModel from "@/model/ClipModel";
import CordovaModel from "@/model/CordovaModel";
import UGCRes from "@/model/UGCResourceManager";

export default {
    data() {
        return {
            thumbUrl: "",
            exportProgress: 0,
            output:{
                desSrc:""
            },
            error: ""
        }
    },
    methods:{
        async begin(){
            let config = await Editlist.editlist.getConfig();
            if(!config.output) {
                // 
                config.output = cordova.file.cacheDirectory + "output.mp4";
                Editlist.editlist.setConfig(config);
            }

            // SDK
            let des = config.output.replace("file://","");


            let ret = await new Promise( UGCApp.ffmpeg.start.bind(null,
                Editlist.getExportParam(des)
            ));
            if(ret){
                this.error = "" + ret;
                return;
            }

            let total_sec = Editlist.editlist.total_sec;
            let progress_sec = 0;
            let status;
            let progress;

            window.console.log("total_sec: "+total_sec);
            await new Promise((o, ) => {
                setTimeout(o, 1000);
            });

            do {
                status = await new Promise( UGCApp.ffmpeg.status );
                progress = await new Promise( UGCApp.ffmpeg.progress );
                progress_sec = progress.pts / 1000;

                this.exportProgress = +Math.max(0, Math.min(100,
                    progress_sec / total_sec * 100)).toFixed(1);

                await new Promise((o, ) => {
                    setTimeout(o, 200);
                });
                window.console.log(
                    "ffmpeg progress:" + progress.pts + ", status:" + status
                );
            } while (status == 1);

            let result = await new Promise(UGCApp.ffmpeg.report);

            if (result == 0) {
                if (CordovaModel.isIOS()) {
                    await new Promise((o, x) => UGCApp.sendToDCIM(des, o, x));
                    this.output.desSrc = (await ClipModel.Util.getCdvUrl(des)).replace("cdvfile:","http:")
                }
                else {
                    await new Promise((o, x) => UGCApp.sendToDCIM(des, o, x));
                    this.output.desSrc = await ClipModel.Util.getCdvUrl(des);
                }
            } else {
                this.error = "";
            }
        },
        async cancel(){
            await new Promise( UGCApp.ffmpeg.cancel );
            this.$emit('close')
        },
        async done(){
            // 
            let config = await Editlist.editlist.getConfig();
            if(config.output_thumb) {
                try {
                    await this.copyPosterTo(config.output_thumb);
                } catch (error) {
                    window.console.log("copy poster: ", error);
                    this.error = "";
                }
            }
            
            await UGCRes.clearTempFiles();

            await new Promise( UGCApp.done.bind(UGCApp, {
                statusCode: UGCApp.StatusCode.Ok,
                message: this.error || "",
                output: config.output,
                output_thumb: config.output_thumb || Editlist.editlist.poster.url
            }) );
        },
        async copyPosterTo(outputFile) {
            let posterUrl = Editlist.editlist.poster.url;
            if('/' == posterUrl[0]) {
                posterUrl = "file://" + posterUrl;
            }

            let root = await new Promise(
                resolveLocalFileSystemURL.bind(window, "file:///"));
            let outFileEntity = await new Promise(
                root.getFile.bind(root, outputFile, {create:true}));
            let outDirEntity = await new Promise(outFileEntity.getParent.bind(outFileEntity));
            let posterFileEntity = await new Promise(
                resolveLocalFileSystemURL.bind(window, posterUrl));

            return await new Promise(posterFileEntity.copyTo.bind(posterFileEntity, outDirEntity, outFileEntity.name));
        }
    },
    async mounted() {
        if(Editlist.editlist.poster.url)
            this.thumbUrl = await ClipModel.Util.getCdvUrl(
                Editlist.editlist.poster.url
            ) + "?" + Math.random();

        document.addEventListener("backbutton", this.cancel, false);
        this.begin();
    },
    async beforeDestroy() {
        document.removeEventListener("backbutton", this.cancel, false);
    }
}
</script>
