// 
// 
// 
// let m = new Magnet(...);
// newLayout = m.check(oldLayout,newLayout, null, ondock);
// xxdom.style.left = newLayout.left;
// xxdom.style.top = newLayout.top;
// 
// layout:
//  {left, top, right, bottom, width, height}
// 

/**
 * 
 */
const DefaultOptions = {
    span: 5, // 
    features: ['inside', 'outside', 'center', 'middle'], // 
    layouts: [], // 
    dynamicLayouts: function () { // 
        return this.layouts;
    },
    render: function(x, y) { // 
        if(x != null) {
            x;// do someting
        } else if(y != null) {
            y;// do someting
        }
    }
};

/**
 * 
 */
const Features = {
    leftToLeft(span, layout, target) {
        if (layout.left - span <= target.left &&
            layout.left + span >= target.left) {
            return {
                diff: layout.left - target.left,
                from: 'left',
                to: 'left'
            };
        }
    },
    leftToRight(span, layout, target) {
        if (layout.left - span <= target.right &&
            layout.left + span >= target.right) {
            return {
                diff: layout.left - target.right,
                from: 'left',
                to: 'right'
            };
        }
    },
    rightToLeft(span, layout, target) {
        if (layout.right - span <= target.left &&
            layout.right + span >= target.left) {
            return {
                diff: layout.right - target.left,
                from: 'right',
                to: 'left'
            };
        }
    },
    rightToRight(span, layout, target) {
        if (layout.right - span <= target.right &&
            layout.right + span >= target.right) {
            return {
                diff: layout.right - target.right,
                from: 'right',
                to: 'right'
            };
        }
    },
    leftToCenter(span, layout, target) {
        let targetHF = target.left + target.width / 2;
        if (layout.left - span <= targetHF &&
            layout.left + span >= targetHF) {
            return {
                diff: layout.left - targetHF,
                from: 'left',
                to: 'center'
            };
        }
    },
    rightToCenter(span, layout, target) {
        let targetHF = target.left + target.width / 2;
        if (layout.right - span <= targetHF &&
            layout.right + span >= targetHF) {
            return {
                diff: layout.right - targetHF,
                from: 'right',
                to: 'center'
            };
        }
    },
    centerToCenter(span, layout, target) {
        let layoutHF = layout.left + layout.width / 2;
        let targetHF = target.left + target.width / 2;
        if (layoutHF - span <= targetHF &&
            layoutHF + span >= targetHF) {
            return {
                diff: layoutHF - targetHF,
                from: 'center',
                to: 'center'
            };
        }
    },
    centerToLeft(span, layout, target) {
        let layoutHF = layout.left + layout.width / 2;
        if (layoutHF - span <= target.left &&
            layoutHF + span >= target.left) {
            return {
                diff: layoutHF - target.left,
                from: 'center',
                to: 'center'
            };
        }
    },
    centerToRight(span, layout, target) {
        let layoutHF = layout.left + layout.width / 2;
        if (layoutHF - span <= target.right &&
            layoutHF + span >= target.right) {
            return {
                diff: layoutHF - target.right,
                from: 'center',
                to: 'center'
            };
        }
    },
    topToTop(span, layout, target){
        if (layout.top - span <= target.top &&
            layout.top + span >= target.top) {
            return {
                diff: layout.top - target.top,
                from: 'top',
                to: 'top'
            };
        }
    },
    topToBottom(span, layout, target){
        if (layout.top - span <= target.bottom &&
            layout.top + span >= target.bottom) {
            return {
                diff: layout.top - target.bottom,
                from: 'top',
                to: 'bottom'
            };
        }
    },
    bottomToTop(span, layout, target){
        if (layout.bottom - span <= target.top &&
            layout.bottom + span >= target.top) {
            return {
                diff: layout.bottom - target.top,
                from: 'bottom',
                to: 'top'
            };
        }
    },
    bottomToBottom(span, layout, target){
        if (layout.bottom - span <= target.bottom &&
            layout.bottom + span >= target.bottom) {
            return {
                diff: layout.bottom - target.bottom,
                from: 'bottom',
                to: 'bottom'
            };
        }
    },
    topToMiddle(span, layout, target){
        let targetHF = target.top + target.height / 2;
        if (layout.top - span <= targetHF &&
            layout.top + span >= targetHF) {
            return {
                diff: layout.top - targetHF,
                from: 'top',
                to: 'middle'
            };
        }
    },
    bottomToMiddle(span, layout, target){
        let targetHF = target.top + target.height / 2;
        if (layout.bottom - span <= targetHF &&
            layout.bottom + span >= targetHF) {
            return {
                diff: layout.bottom - targetHF,
                from: 'bottom',
                to: 'middle'
            };
        }
    },
    middleToMiddle(span, layout, target) {
        let layoutHF = layout.top + layout.height / 2;
        let targetHF = target.top + target.height / 2;
        if (layoutHF - span <= targetHF &&
            layoutHF + span >= targetHF) {
            return {
                diff: layoutHF - targetHF,
                from: 'middle',
                to: 'middle'
            };
        }
    },
    middleToTop(span, layout, target){
        let layoutHF = layout.top + layout.height / 2;
        if (layoutHF - span <= target.top &&
            layoutHF + span >= target.top) {
            return {
                diff: layoutHF - target.top,
                from: 'middle',
                to: 'top'
            };
        }
    },
    middleToBottom(span, layout, target){
        let layoutHF = layout.top + layout.height / 2;
        if (layoutHF - span <= target.bottom &&
            layoutHF + span >= target.bottom) {
            return {
                diff: layoutHF - target.bottom,
                from: 'middle',
                to: 'bottom'
            };
        }
    },
    inside(span, layout, target) {
        return [
            Features.leftToLeft.call(this, span, layout, target),
            Features.rightToRight.call(this, span, layout, target),
            Features.topToTop.call(this, span, layout, target),
            Features.bottomToBottom.call(this, span, layout, target)
        ].filter(r => r);
    },
    outside(span, layout, target) {
        return [
            Features.leftToRight.call(this, span, layout, target),
            Features.rightToLeft.call(this, span, layout, target),
            Features.topToBottom.call(this, span, layout, target),
            Features.bottomToTop.call(this, span, layout, target)
        ].filter(r => r);
    },
    center(span, layout, target) {
        return [
            Features.leftToCenter.call(this, span, layout, target),
            Features.rightToCenter.call(this, span, layout, target),
            Features.centerToCenter.call(this, span, layout, target),
            Features.centerToLeft.call(this, span, layout, target),
            Features.centerToRight.call(this, span, layout, target)
        ].filter(r => r);
    },
    middle(span, layout, target) {
        return [
            Features.topToMiddle.call(this, span, layout, target),
            Features.bottomToMiddle.call(this, span, layout, target),
            Features.middleToMiddle.call(this, span, layout, target),
            Features.middleToTop.call(this, span, layout, target),
            Features.middleToBottom.call(this, span, layout, target)
        ].filter(r => r);
    }
}

export default class Magnet {
    constructor(options = DefaultOptions) {
        this.options = Object.assign({}, DefaultOptions, options);
        this.features = Object.create(Features);
    }
    /**
     * layout
     */
    findTargets(layout, features) {
        features = features || this.options.features;

        let span = this.options.span;
        return this.options.dynamicLayouts().flatMap(l =>
            features.map(feature => this.features[feature])
            .flatMap(featureFn => featureFn.call(this, span, layout, l))
            .filter(target => target)
            .map(target => {
                target.layout = l;
                return target;
            }))
    }
    getTargetPosition(target){
        if (target.to == 'center') {
            return target.layout.left + target.layout.width / 2;
        } else if(target.to == 'middle') {
            return target.layout.top + target.layout.height / 2;
        } else {
            return target.layout[target.to];
        }
    }
    /**
     * layout
     */
    createLayout(layout, xTarget , yTarget) {
        layout = Object.assign({}, layout);
        if (xTarget) {
            // to
            let x = this.getTargetPosition(xTarget);
            // from
            if (xTarget.from == 'center') {
                layout.left = x - layout.width / 2;
                layout.right = layout.left + layout.width;
            } else {
                layout[xTarget.from] = x;
                if(xTarget.from == 'left') {
                    layout.right = layout.left + layout.width;
                } else if (xTarget.from == 'right') {
                    layout.left = layout.right - layout.width;
                }
            }
        }
        if(yTarget) {
            // to
            let y = this.getTargetPosition(yTarget);
            // from
            if (yTarget.from == 'middle') {
                layout.top = y - layout.height / 2;
                layout.bottom = layout.top + layout.height;
            } else {
                layout[yTarget.from] = y;
                if(yTarget.from == 'top') {
                    layout.bottom = layout.top + layout.height;
                } else if (yTarget.from == 'bottom') {
                    layout.top = layout.bottom - layout.height;
                }
            }
        }
        return layout;
    }
    /**
     * layout
     * layout
     * @param layout layout
     * @param newLayout layout
     * @param features 
     */
    check(layout, newLayout, features) {
        let targets = this.findTargets(newLayout, features);

        // 
        let xTarget = null;
        let yTarget = null;
        let x = null;
        let y = null;

        targets.forEach(t => {
            if(t.to == 'left' || t.to == 'center' || t.to == 'right') {
                if(!xTarget) {
                    xTarget = t;
                } else if (t.to == Math.abs(t.diff) < Math.abs(xTarget.diff)) {
                    xTarget = t;
                }
            } else {
                if(!yTarget) {
                    yTarget = t;
                } else if (Math.abs(t.diff) < Math.abs(yTarget.diff)) {
                    yTarget = t;
                }
            }
        });

        if (xTarget || yTarget) {
            if(xTarget) {
                x = this.getTargetPosition(xTarget);
            }
            if(yTarget) {
                y = this.getTargetPosition(yTarget);
            }

            newLayout = this.createLayout(newLayout, xTarget, yTarget);
        }
        this.options.render(x, y);
        return newLayout;
    }
}