<template>
<div class="aspect-box">

    <div >
        <ClipBtn
            :active="!curAspectRatio"
            @click.native="choose(null)"
            class="item-btn" 
            :icon="'phonelink_lock'" :text="''" >
        </ClipBtn>
        <ClipBtn v-for="(e,idx) in AspectRatio" :key="e[0]+':'+e[1]" 
            :active="curAspectRatio == e"
            @click.native="choose(e)"
            class="item-btn" 
            :src="AspectRatioIcon[idx]" :text="e[0]+':'+e[1]" >
        </ClipBtn>
    </div>
</div>
</template>
<style scoped>

.aspect-box{
    display: flex;
    flex-flow: row;
    width:100%;
}
.aspect-box > div{
    display: flex;
    flex-flow: row;
    width:100%;
    justify-content: space-evenly
}
</style>
<script>
import Editlist from "@/model/Editlist";
import ClipBtn from "@/components/ClipBtn";
import Const from "@/const";

let AspectRatio = [
    [9,16],
    [16,9],
    [1,1],
    [3,4],
    [4,3]
]

async function createIcons(AspectRatio){
    return await Promise.all(AspectRatio.map(async i => {
        
        let PADDING = 30;
        let BOX = 100;

        let can = document.createElement("canvas");
        can.width = BOX + PADDING*2;
        can.height = BOX + PADDING*2;
        let ctx = can.getContext("2d");
        ctx.fillStyle = "white";
        let ratio = i[0]/i[1];
        if(ratio > 1){
            ctx.fillRect(
                PADDING, PADDING + (BOX - BOX / ratio)/2,
                BOX, BOX / ratio);
        } else {
            ctx.fillRect(
                PADDING + (BOX - BOX * ratio)/2, PADDING,
                BOX * ratio, BOX);
        }
        return URL.createObjectURL(await new Promise(can.toBlob.bind(can)))
        
    }));
}

export default {
    components:{ClipBtn},
    props:["editlist"],
    data(){
        return {
            AspectRatio,
            AspectRatioIcon:[],
            curAspectRatio:null
        }
    },
    methods:{
        choose(e){
            if(this.curAspectRatio == e) return;

            this.curAspectRatio = e;
            if(e)
                this.editlist.video.dar = e;
            else
                this.editlist.video.dar = Editlist.DARPreset.auto;

            this.$emit("change")
        },
        onDialogClose() {
            if(this.initState) {
                if(this.initState.aspectRatio != this.curAspectRatio) {
                    this.editlist.createOperateHistory(Const.Operate.VideoAspect.Type);
                }
            }
        }
    },
    async mounted(){
        this.$emit("update:title", "")
        this.AspectRatioIcon = await createIcons(AspectRatio);
        this.curAspectRatio = AspectRatio.filter(i => {
            return i.join(":") == this.editlist.video.dar.join(":");
        })[0]
        this.initState = {
            aspectRatio: this.curAspectRatio
        };
    }
}
</script>